<template>
  <div class="content release">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem>上传应用</BreadcrumbItem>
      </Breadcrumb>
      <Tabs value="apps" @on-click="handleChangeTab">
        <TabPane v-for="item in menus" :key="item.id" :label="item.name" :name="item.id"></TabPane>
      </Tabs>
      <h3>
        <Button @click="$router.push('/apps')" icon="reply" size="large">返回我的应用</Button>
      </h3>
      <div id="upload_area" class="upload-area">
        <template v-if="!isUploading && !isUploaded">
          <Button id="pick_files" type="primary" icon="ios-cloud-upload">立刻上传</Button>
          <p>点击上传选择应用的安装包，或拖拽文件到此区域</p>
          <p>（支持.ipa或.apk文件，单个文件最大支持600M）</p>
          <p>还可上传 {{amount}} 个文件</p>
          <div
            id="html5_1c2vcongc1unmi0e18v1rjvjul4_container"
            class="moxie-shim moxie-shim-html5"
            style="position:absolute;top:130px;left:50%;height:60px;width:240px;margin-left:-120px;overflow:hidden;z-index:0;">
            <input
              id="html5_1c2vcongc1unmi0e18v1rjvjul4"
              type="file"
              style="font-size:999px;opacity:0;position:absolute;top:0;left:0;width:100%;height:100%;"
              multiple=""
              accept="">
          </div>
        </template>
        <template v-if="isUploading && !isUploaded">
          <div class="uploading">
            <p v-if="logoBase64"><img class="app-logo" :src="`data:image/jpeg;base64,${logoBase64}`" height="64" width="64" alt=""></p>
            <p>应用上传中，请不要关闭浏览器...</p>
            <Progress :percent="percent"></Progress>
            <p>已经上传 {{uploadedText}} / 文件大小 {{fileSize}} MB</p>
            <p><Button v-if="!isPaused" type="warning" @click="pauseUpload">暂停上传</Button><Button v-else type="success" @click="goOnUpload">继续上传</Button></p>
          </div>
        </template>
        <template v-if="isUploaded">
          <div class="uploaded">
            <p v-if="logoBase64"><img class="app-logo" :src="`data:image/jpeg;base64,${logoBase64}`" height="64" width="64" alt=""></p>
            <h3>如果长时间没有反应请联系 <a href="http://wpa.qq.com/msgrd?v=3&uin=1455653215&site=qq&menu=yes" target="_blank">QQ:1455653215</a> 处理</h3>
            <p>应用上传完成，正在更新包信息...</p>
          </div>
        </template>
      </div>
      <h3 class="warning">严禁上传色情、反动、非法直播、非法博彩类 APP，违反平台规则的 APP 将被删除处理，屡次上传者将被封禁, 请配合。</h3>
      <Alert type="warning" show-icon>提供iOS企业签名服务，支持按月/季度/半年/全年，长久稳定。请联系客服<a
        href="http://wpa.qq.com/msgrd?v=3&amp;uin=3261682395&amp;site=qq&amp;menu=yes" target="_blank">
        QQ：3261682395</a></Alert>
    </div>
  </div>
</template>

<script>
import Base64 from 'base-64'

export default {
  name: 'PageRelease',
  data () {
    return {
      menus: [
        {id: 'apps', name: '我的应用'},
        {id: 'apps_log', name: '下载日志'},
        {id: 'apps_list', name: '应用列表'}
      ],
      amount: 0,
      uploader: null,
      //  上传组件状态缓存
      isAllowed: true,
      isUploading: false,
      isUploaded: false,
      isPaused: false,
      percent: 0,
      uploadedText: '',
      fileSize: 0,
      //  上传组件初始化参数
      upConfig: {
        allowsize: 15728640000,
        uploadedsize: 1042560011,
        singlesize: 524288000,
        allow_num: 88,
        uptoken: '',
        qndomain: 'http://down.dafu.im/',
        flash_swf_url: '/static/lib/Moxie.swf'
      },
      zipEntries: [],
      emptyBase64: '',
      logoBase64: '',
      //  应用上传标识，存在则覆盖升级，不存在则新建
      localId: '',
      //  新建或升级应用信息
      appInfo: {
        apk_name: '',
        logo: '',
        hext: '',
        app_name: '',
        version: '',
        package_name: '',
        version_code: '',
        down_link: '',
        app_size: ''
      }
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.checkUserInfo()
      //  如果存在 localId 则为升级应用，否则为新建
      this.localId = this.$route.query.id
      //  获取 uptoken
      this.$ajax.get('/api/upload/getToken').then((res) => {
        if (res.data && res.data.code === '200') {
          this.upConfig.uptoken = res.data.data
          this.initUploader()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    checkUserInfo () {
      const userInfo = this.$store.userInfo
      if (!userInfo.limit_num) {
        setTimeout(() => {
          this.checkUserInfo()
        }, 0)
      } else {
        this.amount = userInfo.limit_num - userInfo.used_num
        if (!this.amount || this.amount < 0) this.amount = 0
      }
    },
    handleChangeTab (path) {
      this.$router.push(`/${path}`)
    },
    //  初始化上传组件
    initUploader () {
      const Qiniu = window.Qiniu
      const plupload = window.plupload
      const self = this
      this.uploader = Qiniu.uploader({
        runtimes: 'html5, flash, html4',
        browse_button: 'pick_files',
        uptoken: this.upConfig.uptoken,
        unique_names: true,
        save_key: false,
        domain: this.upConfig.qndomain,
        get_new_uptoken: false,
        container: 'upload_area',
        flash_swf_url: this.upConfig.flash_swf_url,
        max_retries: 3,
        dragdrop: true,
        drop_element: 'upload_area',
        chunk_size: '4mb',
        auto_start: false,
        multi_selection: false,
        filters: {
          max_file_size: '5000mb',
          prevent_duplicates: true,
          mime_types: [
            {title: 'app files', extensions: 'apk'},
            {title: 'ipa files', extensions: 'ipa'}
          ]
        },
        init: {
          'FilesAdded' (up, files) {
            plupload.each(files, (file) => {
              if (self.checkFileBeforeUp(file.size)) {
                //  对上传列表文件解包操作
                self.processZipFile(file.getNative())
              }
            })
          },
          'BeforeUpload' (up, file) {
            if (self.isAllowed) {
              self.isUploading = true
              self.fileSize = (file.size / 1024 / 1024).toFixed(2)
            } else {
              self.uploader.stop()
            }
          },
          'UploadProgress' (up, file) {
            if (file.loaded > 1024 * 1024) {
              self.uploadedText = `${(file.loaded / 1024 / 1024).toFixed(2)} MB`
            } else {
              self.uploadedText = `${(file.loaded / 1024).toFixed(2)} KB`
            }
            self.percent = file.percent
          },
          'FileUploaded' (up, file, info) {
            const res = JSON.parse(info)
            const domain = up.getOption('domain')
            self.appInfo.apk_name = res.key
            self.appInfo.app_size = file.size
            self.appInfo.down_link = domain + res.key
            self.appInfo.hext = Qiniu.getFileExtension(file.name)
            self.isUploaded = true
            self.updateAppInfo(self.appInfo, self.localId)
          },
          'Error' (up, err, errTip) {
            self.$Message.error(errTip)
            //  return false
          },
          'UploadComplete' () {
          },
          'Key' (up, file) {
            return file.name
          }
        }
      })
    },
    //  验证文件信息
    checkFileBeforeUp (size) {
      if (!this.localId && this.upConfig.allowNum < 0) {
        this.isAllowed = false
        this.$Message.warning('上传APP个数超过限制，请删除过期的APP或联系网站客服QQ: 3261682395 进行空间升级')
        return false
      }
      if (size + this.upConfig.uploadedSize > this.upConfig.allowSize) {
        this.isAllowed = false
        this.$Message.warning('您的账号空间不足，请删除过期的APP或联系网站客服QQ: 3261682395 进行空间升级')
        return false
      }
      if (size > this.upConfig.singleAllow) {
        this.isAllowed = false
        this.$Message.warning('单文件太大了，超过' + Math.floor((this.upConfig.singleAllow / 1024 / 1024) * 100) / 100 + 'MB啦')
        return false
      } else {
        return true
      }
    },
    //  暂停上传
    pauseUpload () {
      this.isPaused = true
      this.uploader.stop()
    },
    //  继续上传
    goOnUpload () {
      this.isPaused = false
      this.uploader.start()
    },
    //  解包
    processZipFile (file) {
      const zip = window.zip
      const self = this
      zip.createReader(new zip.BlobReader(file), (reader) => {
        reader.getEntries((entries) => {
          self.zipEntries = entries
          let infoplist = null
          let resoucesArsc = null
          let androidManifest = null
          for (let i = 0, l = entries.length; i < l; i++) {
            if (!entries[i].filename.match(/__MACOSX/i) && entries[i].filename.match(/Payload\/[\w-]+\.app\/Info\.plist/)) {
              infoplist = entries[i]
              break
            }
            if (entries[i].filename.match(/AndroidManifest.xml/)) {
              androidManifest = entries[i]
              if (resoucesArsc) break
            }
            if (entries[i].filename.match(/resources.arsc/)) {
              resoucesArsc = entries[i]
              if (androidManifest) break
            }
          }
          self.processZipFileSubmit(infoplist, resoucesArsc, androidManifest)
        })
      }, (err) => {
        console.log(err)
        this.$Message.error('读取安装包信息失败!')
      })
    },
    processZipFileSubmit (infoplist, resoucesArsc, androidManifest) {
      const zip = window.zip
      const data = {}
      const parseFiles = this.parseFiles
      if (infoplist) {
        data.system = 'ios'
        infoplist.getData(new zip.BlobWriter(), (blob) => {
          const reader = new FileReader()
          reader.addEventListener('loadend', () => {
            const bytes = new Uint8Array(reader.result)
            data.plist = Base64.encode(bytes)
            parseFiles(data)
          })
          reader.readAsArrayBuffer(blob)
        })
      } else if (resoucesArsc && androidManifest) {
        data.system = 'android'
        readArsc(resoucesArsc, androidManifest)
      } else {
        this.$Message.error('上传的文件不正确,无法读取')
        return false
      }
      function readArsc (resoucesArsc, androidManifest) {
        resoucesArsc.getData(new zip.BlobWriter(), (blob) => {
          const reader = new FileReader()
          reader.addEventListener('loadend', () => {
            data.arsc = Base64.encode(new Uint8Array(reader.result))
            readManifest(androidManifest)
          })
          reader.readAsArrayBuffer(blob)
        })
      }
      function readManifest (androidManifest) {
        androidManifest.getData(new zip.BlobWriter(), (blob) => {
          const reader = new FileReader()
          reader.addEventListener('loadend', () => {
            data.mainifest = Base64.encode(new Uint8Array(reader.result))
            parseFiles(data)
          })
          reader.readAsArrayBuffer(blob)
        })
      }
    },
    //  解析应用信息
    parseFiles (data) {
      const url = data.system === 'ios' ? 'parsePlist' : 'parseAndroid'
      this.$ajax.post(`/api/upload/${url}`, data).then((res) => {
        if (res.data && res.data.code === '200') {
          const d = res.data.data
          this.parseLogo(d, data.system, (logoBase64) => {
            this.appInfo.app_name = d.Name
            this.appInfo.version = d.Version
            this.appInfo.package_name = d.PackageName
            this.appInfo.version_code = d.VersionCode
            this.appInfo.logo = logoBase64 || this.emptyBase64
            this.logoBase64 = logoBase64
            //  解包完毕，开始上传
            this.uploader.start()
          })
        } else {
          this.$Message.error('安装包存在问题，请联系管理员处理')
          this.uploader.stop()
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    //  解析应用图标
    parseLogo (data, system, callback) {
      const zip = window.zip
      let logoName = data.LogoName
      if (system === 'ios') {
        logoName = `/${logoName}`
        if (!~logoName.indexOf('.')) {
          logoName += '([\\d]+x[\\d]+)?(@[\\d]x)?.png'
        }
      }
      const logo = this.getIconFile(logoName)
      if (!logo) {
        this.appInfo.logo = this.emptyBase64
      } else {
        logo.getData(new zip.BlobWriter(), (blob) => {
          const reader = new FileReader()
          reader.addEventListener('loadend', () => {
            const image = Base64.encode(new Uint8Array(reader.result))
            this.$ajax.post('/api/upload/parseLogo', {image}).then((res) => {
              if (res.data && res.data.code === '200') {
                callback(res.data.data)
              } else {
                callback()
              }
            }).catch((err) => {
              console.log(err)
              callback()
            })
          })
          reader.readAsArrayBuffer(blob)
        })
      }
    },
    getIconFile (fileName) {
      let iconFile = null
      for (let i = 0, l = this.zipEntries.length; i < l; i++) {
        if (this.zipEntries[i].filename.match(new RegExp(fileName, 'i'))) {
          iconFile = this.zipEntries[i]
          break
        }
      }
      return iconFile
    },
    //  更新应用信息
    updateAppInfo (data, id) {
      const url = id ? 'upgrade' : 'create'
      let d = data
      if (id) d.iLocalId = id
      this.$ajax.post(`/api/upload/${url}`, d).then((res) => {
        if (res.data && res.data.code === '200') {
          const router = this.$router
          this.$Message.success({
            content: '上传文件成功，即将跳转...',
            duration: 3,
            onClose () {
              router.push('/apps')
            }
          })
        } else {
          this.$Message.error({
            content: '上传文件失败，请稍后重试',
            duration: 3,
            onClose () {
              //  window.location.reload()
            }
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.release {
   h3 {
     margin-bottom: 20px;
   }
   .warning {
     font-weight: normal;
     font-size: 14px;
     color: #ed3f14;
   }
   .ivu-upload {
     margin-bottom: 15px;
   }
   .upload-area {
     position: relative;
     margin-bottom: 20px;
     padding: 130px 0;
     border: 1px dashed #e1f0fe;
     background: #fff;
     text-align: center;
     button {
       height:60px;
       width:240px;
       margin-bottom: 20px;
       font-size:20px;
     }
   }
 }
.uploading, .uploaded {
  position: relative;
  z-index: 99;
  h3 {
    font-size: 18px;
  }
  p {
    margin: 10px 0;
  }
}
.app-logo {
  border-radius: 16%;
}
</style>



