<template>
  <div class="content pay-weixin">
    <div class="wrapper">
      <Card :padding="45" shadow>
        <h2 slot="title">微信支付</h2>
        <div class="inner-box" :style="`background:url(${weixin}) no-repeat 100% 50%`">
          <div class="qrcode">
            <canvas id="qrcode_canvas"></canvas>
          </div>
          <h3><Icon type="arrow-up-b"/>请使用手机微信<br/>扫描上方二维码进行支付</h3>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import qrCode from 'qrcode'
import weixin from '../assets/images/iphone_weixin.png'

export default {
  name: 'PagePayWeixin',
  data () {
    return {
      weixin
    }
  },
  created () {
  },
  mounted () {
    this.$nextTick(function () {
      this.canvas = document.getElementById('qrcode_canvas')
      const url = decodeURIComponent(this.$route.query.qr_code)
      qrCode.toCanvas(this.canvas, url, (error) => {
        if (error) {
          console.log(error)
        } else {
          //  this.isWeixinpay = true
        }
      })
    })
    const tradeId = this.$route.query.order_sn
    const router = this.$router
    const queryTradeStatus = () => {
      this.$ajax.get('/order/is-paid?order_sn=' + tradeId).then((res) => {
        if (res.data && res.data.code === '200') {
          const data = res.data.data || {}
          if (String(data.is_paid) === '1') {
            clearInterval(queryTradeStatusTimer)
            const jump = {}
            if (String(data.is_paid) === '1') {
              jump.path = '/pack_setting'
              jump.name = '封装操作'
            }
            this.$Message.success({
              content: `支付成功，即将跳转到${jump.name}页面 ...`,
              duration: 3,
              onClose () {
                router.push(jump.path)
              }
            })
          }
        }
      })
    }
    const queryTradeStatusTimer = setInterval(queryTradeStatus, 2000)
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.pay-weixin {
  h2 {
    text-align: center;
    font-weight: normal;
    font-size: 18px;
  }
}
.inner-box {
  height: 420px;
  .qrcode {
    height: 150px;
    width: 150px;
    margin: 0 auto 20px;
    border: 1px solid #eee;
    canvas {
      display: block;
    }
  }
  h3 {
    line-height: 30px;
    text-align: center;
    .ivu-icon {
      margin-right: 5px;
    }
  }
}
</style>



