<template>
  <div class="apps-preview">
    <Card shadow>
      <h2 slot="title">{{appInfo.app_name}}</h2>
      <div class="icon">
        <img
          :src="`//pic.dafu.im/icons/${appInfo.id}.png?${new Date(appInfo.update_dt).getTime()}&imageView2/1/w/120/h/120//ignore-error/1`"
          height="120"
          width="120"
          alt="">
      </div>
      <div class="app-info">
        <Alert><span>版本：{{appInfo.version}}</span><span>大小：{{(appInfo.app_size / 1000000).toFixed(2)}}M</span><span>更新时间：{{appInfo.update_dt}}</span></Alert>
        <canvas id="qr_code"></canvas>
      </div>
    </Card>
  </div>
</template>

<script>
export default {
  name: 'PageAppsPreview',
  data () {
    return {
      appInfo: {}
    }
  },
  created () {
    const id = this.$route.params.id
    this.$ajax.get('/api/apps/detail', {params: {id}}).then((res) => {
      if (res.data && res.data.code === '200') {
        this.appInfo = res.data.data
      } else {
        this.$Message.error(res.data.msg)
      }
    }).catch((err) => {
      console.log(err)
    })
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.apps-preview {
  text-align: center;
}
.icon {
  margin-bottom: 20px;
  img {
    display: block;
    margin: auto;
  }
}
.app-info .ivu-alert-message span {
  margin: 0 5px;
}
</style>



