          <div style="line-height: 23px;">1.安卓包可以直接安装，苹果包需要做企业签名才能安装。</div>
          <div style="line-height: 23px; color: #3f8ce6;">签名客服QQ： 326168235</div>
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import GetFileImageBase64 from '../utils/GetFileImageBase64'
  import phonePreviewImage from '../assets/images/phone_preview_icon.png'
  import defaultStartImage from '../assets/images/default_start_image.png'
  import freshOnPull from '../assets/images/fresh_on_pull.png'
  import backAndFreshButton from '../assets/images/back_and_fresh_button.png'
  import canvasQrcode from 'qrcode'

  export default {
    name: 'PagePackSetting',
    data () {
      return {
        image1: '',
        currentStep: 999,
        serviceId: '100012',
        serviceIndex: 0,
        restFrequency: 0,
        phonePreviewIconBg: phonePreviewImage,
        defaultStartImage: defaultStartImage,
        freshOnPull: freshOnPull,
        backAndFreshButton: backAndFreshButton,
        colorBoxColors: [
          '#1a79fb',
          '#dc4729',
          '#f27822',
          '#15819d',
          '#2865cf',
          '#ed488c'
        ],
        serviceModelList: [
          {
            name: '基础版',
            productId: '100011',
            step: [
              'step1'
            ],
            allowItems: {
              appName: true,
              appUrl: true,
              device: true,
              appIcon: true,
              splashImage: true
            }
          }, {
            name: '中级版',
            productId: '100012',
            step: [
              'step1',
              'step2'
            ],
            allowItems: {
              appName: true,
              appUrl: true,
              device: true,
              appIcon: true,
              appIconColor: true,
              splashImage: true,
              splashTime: true,
              appVersion: true,
              browserCoreAndroid: true,
              browserCoreiOS: true,
              schemeSupport: true,
              clearCache: true,
              freshOnPull: true,
              backAndFreshButton: true
            }
          }, {
            name: '高级版',
            productId: '100013',
            step: [
              'step1',
              'step2'
            ],
            allowItems: {
              appName: true,
              appUrl: true,
              device: true,
              appIcon: true,
              appIconColor: true,
              splashImage: true,
              splashTime: true,
              appVersion: true,
              browserCoreAndroid: true,
              browserCoreiOS: true,
              schemeSupport: true,
              clearCache: true,
              freshOnPull: true,
              backAndFreshButton: true
            }
          }
        ],
        step1: {
          appName: [
            {required: true, message: 'APP名称不能为空', trigger: 'click'},
            {pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9\\.]{1,6}$/, message: 'APP名称不正确', trigger: 'change'}
          ],
          appUrl: [
            {required: true, message: 'APP网址不能为空', trigger: 'click'},
            {pattern: /^((https|http|ftp|rtsp|mms)?:\/\/)[^\s]+$/, message: '网址格式不正确', trigger: 'blur'}
          ]
        },
        step2: {
          appVersion: [
            {required: true, message: 'APP版本号不能为空', trigger: 'click'},
            {pattern: /^[^\u4e00-\u9fa5\s]+$/, message: 'APP版本号不正确', trigger: 'change'}
          ]
        },
        appSetting: {
          appName: '',
          appUrl: '',
          device: '',
          appIconText: '',
          appIcon: '',
          appIconColor: '#1a79fb',
          splashMode: 1,
          splashImage: [],
          splashTime: '2',
          appVersion: '1.0.0',
          browserCoreAndroid: 'X5WebView',
          browserCoreiOS: 'WKWebView',
          schemeSupport: false,
          clearCache: false,
          freshOnPull: false,
          backAndFreshButton: false
        },
        modalConfirm: false,
        modalGenerate: false,
        modalGenerateData: {
          qrcode: '',
          progressTimer: '',
          restTime: 1800,
          expireTime: '00:30:00',
          closeable: false,
          androidButton: {
            disable: true,
            progress: 0,
            text: '正在生成'
          },
          iOSButton: {
            disable: true,
            progress: 0,
            text: '正在生成'
          },
          progressWorkingText: '准备封装资源 ...'
        },
        confirmGenerateLoadingFlag: false,
        appPackId: '',
        appPackTestPage: '',
        appPackLevel: ''
      }
    },
    created () {
      this.currentStep = 0
      this.$ajax.get('user/my-services?type=1', {type: 1}).then((response) => {
        this.appPackLevel = response.data.data.level
        this.serviceIndex = response.data.data.level - 1
        this.restFrequency = response.data.data.limited_nums || 0
      })
    },
    mounted () {
      // jColorPicker颜色选择器
      const { jQuery } = window
      setTimeout(() => {
        jQuery.farbtastic(jQuery('.color-picker') || [{}], (colorValue) => {
          this.appSetting.appIconColor = colorValue
        })
      }, 1000)
      // 特殊的按钮背景进度条
      const processbarStyle = document.createElement('style')
      processbarStyle.innerHTML =
        '.progress-button .ivu-progress-inner { border-radius: 4px; width: 205px; }' +
        '.progress-button .ivu-progress-bg { border-radius: 4px; height: 52px!important; }' +
        '.progress-button .ivu-progress-success .ivu-progress-bg { background-color: #3a9bfc }' +
        '.progress-button button[progress="100"][disabled] .button-text { background-color: #c3cbd6 }'
      document.body.appendChild(processbarStyle)
      // 输入APP名称自动填入图标字
      this.$watch('appSetting.appName', function (appName) {
        this.appSetting.appIconText = appName[0] || ''
      })
      // 特殊的输入框验证
      this.$watch('appSetting.appIconText', function (cv, ov) {
        if (cv === ' ') {
          this.appSetting.appIconText = ''
        }
      })
      this.$watch('appSetting.device', function (cv, ov) {
        if (cv === '3' && this.restFrequency < 2) {
          this.$Modal.confirm({
            content: `<h3 style="margin-bottom:10px; color: #f00">当前${this.serviceModelList[this.serviceIndex].name}套餐剩余1次，<br />需要购买次数后才可以制作！</h3>`,
            okText: '购买套餐',
            cancelText: '取消',
            onOk: () => {
              console.log('余额不足跳转: OK')
              location.href = '/pack_purchase?mode=purchase'
            },
            onCancel: () => {
              console.log('余额不足跳转: Cancel')
            }
          })
        }
      })
    },
    methods: {
      handleIconUploadClick (event) {
        const uploadInput = event.target.parentNode.getElementsByClassName('icon-upload-input')[0]
        uploadInput.click()
      },
      handleAppIconUpload (event) {
        GetFileImageBase64(event.target).then((imageDataList) => {
          this.appSetting.appIcon = imageDataList[0]
          setTimeout(() => {
            event.target.value = null
          }, 1000)
        })
      },
      handlesplashImageUploadClick (event) {
        const uploadInput = document.getElementsByClassName('start-page-upload-input')[0]
        uploadInput.click()
      },
      handlesplashImageUpload (event) {
        GetFileImageBase64(event.target).then((imageDataList) => {
          if (imageDataList.length) {
            this.appSetting.splashImage.push(imageDataList[0])
            setTimeout(() => {
              event.target.value = null
            }, 1000)
          }
        })
      },
      handleRemovesplashImage (index) {
        this.appSetting.splashImage = this.appSetting.splashImage.filter((e, i) => {
          return !(index === i)
        })
      },
      validateStep (validCallBack) {
        const currentStepName = this.serviceModelList[this.serviceIndex].step[this.currentStep]
        this.$refs[currentStepName].validate((valid) => {
          console.log(this.serviceModelList[this.serviceIndex].step[this.currentStep], valid)
          if (valid) {
            switch (currentStepName) {
              case 'step1':
                if (Boolean(this.appSetting.appIcon) === false && Boolean(this.appSetting.appIconText) === false) {
                  this.$Message.info('请设置APP图标')
                } else if (this.appSetting.device === '') {
                  this.$Message.info('请选择设备类型')
                } else {
                  validCallBack(currentStepName)
                }
                break
              case 'step2':
                validCallBack(currentStepName)
                break
            }
          }
        })
      },
      handleNextStep () {
        this.validateStep(() => {
          this.currentStep++
        })
      },
      generateProgressRunning () {
        const progressIncrease = () => {
          const progressStepsAndroid = {
            0: {
              android: 4,
              iOS: 4
            },
            10: {
              android: 1,
              iOS: 1.5
            },
            30: {
              android: 2,
              iOS: 2
            },
            50: {
              android: 2,
              iOS: 3
            },
            75: {
              android: 2,
              iOS: 2
            },
            95: {
              android: 0.5,
              iOS: 0.5
            },
            99: {
              android: 0,
              iOS: 0
            },
            100: {
              android: 0,
              iOS: 0
            }
          }
          const stepProgress = Object.keys(progressStepsAndroid)
          stepProgress.map((progressNumb, index) => {
            if (index < stepProgress.length - 1) {
              if (this.modalGenerateData.androidButton.progress < stepProgress[index + 1] && progressNumb <= this.modalGenerateData.androidButton.progress) {
                this.modalGenerateData.androidButton.progress += Number(progressStepsAndroid[progressNumb].android)
              }
              if (this.modalGenerateData.iOSButton.progress < stepProgress[index + 1] && progressNumb <= this.modalGenerateData.iOSButton.progress) {
                this.modalGenerateData.iOSButton.progress += Number(progressStepsAndroid[progressNumb].iOS)
              }
              if (this.modalGenerateData.androidButton.progress === 99 && index === 0) {
                this.$Message.error('网络服务异常，生成Android APP失败！')
              }
              if (this.modalGenerateData.iOSButton.progress === 99 && index === 0) {
                this.$Message.error('网络服务异常，生成iOS APP失败！')
              }
              if (this.modalGenerateData.androidButton.progress >= 99 && this.modalGenerateData.iOSButton.progress >= 99) {
                clearInterval(this.modalGenerateData.progressTimer)
              }
            }
          })
          console.log('genProgress:', this.modalGenerateData.androidButton.progress, this.modalGenerateData.iOSButton.progress)
        }
        this.modalGenerateData.progressTimer = setInterval(progressIncrease, 3000)
        const onProgressChange = (progressValue, btnName) => {
          if (this.modalGenerateData['androidButton'].progress > 20 && this.modalGenerateData['androidButton'].progress <= 30) {
            this.modalGenerateData.progressWorkingText = '分析信息 ...'
          }
          if (this.modalGenerateData['androidButton'].progress > 30 && this.modalGenerateData['androidButton'].progress <= 50) {
            this.modalGenerateData.progressWorkingText = '加载原生组件 ...'
          }
          if (this.modalGenerateData['androidButton'].progress > 50 && this.modalGenerateData['androidButton'].progress <= 80) {
            this.modalGenerateData.progressWorkingText = '执行编译 ...'
          }
          if (this.modalGenerateData['androidButton'].progress > 80 && this.modalGenerateData['androidButton'].progress <= 90) {
            this.modalGenerateData.progressWorkingText = '加密资源 ...'
          }
          if (this.modalGenerateData['androidButton'].progress > 90 && this.modalGenerateData['androidButton'].progress <= 99) {
            this.modalGenerateData.progressWorkingText = '重新链接资源 ...'
          }
          if (this.modalGenerateData['androidButton'].progress === 100) {
            this.modalGenerateData.progressWorkingText = '生成完成，即将跳转到APP列表页 ...'
            setTimeout(() => { location.href = '/user/pack/list' }, 4000)
          }
          if (progressValue > 80) {
            if (progressValue > 97) {
              this.modalGenerateData[btnName].progress = 100
              this.modalGenerateData[btnName].disable = false
              this.modalGenerateData[btnName].text = '点击下载'
            }
            if (progressValue === 100 && this.modalGenerateData['androidButton'].progress === 100 && (String(this.appSetting.device) === '1' || String(this.appSetting.device) === '3')) {
              this.downloadLimitTimerRunning()
            }
            if (progressValue === 100 && this.modalGenerateData['iOSButton'].progress === 100 && String(this.appSetting.device) === '2') {
              this.modalGenerateData['androidButton'].progress = 100
              this.downloadLimitTimerRunning()
            }
          }
        }
        this.$watch('modalGenerateData.androidButton.progress', (nVal) => {
          onProgressChange(nVal, 'androidButton')
        })
        this.$watch('modalGenerateData.iOSButton.progress', (nVal) => {
          onProgressChange(nVal, 'iOSButton')
        })
      },
      downloadLimitTimerRunning () {
        const _thisModelScope = this
        const timerRunning = () => {
          if (_thisModelScope.modalGenerateData.restTime === 0) {
            clearInterval(downloadLimitTimer)
          }
          if (_thisModelScope.modalGenerateData.restTime < 1500) {
            this.modalGenerateData.closeable = true
          }
          const hour = '00'
          const minute = String(100 + Math.floor(_thisModelScope.modalGenerateData.restTime / 60))
          const second = String(100 + _thisModelScope.modalGenerateData.restTime - Math.floor(_thisModelScope.modalGenerateData.restTime / 60) * 60)
          _thisModelScope.modalGenerateData.restTime -= 1
          _thisModelScope.modalGenerateData.expireTime = `${hour}:${minute[1]}${minute[2]}:${second[1]}${second[2]}`
        }
        let downloadLimitTimer = setInterval(timerRunning, 1000)
      },
      handleGenerateApp () {
        this.validateStep(() => {
          const appConfig = {}
          Object.keys(this.appSetting).map((key, index) => {
            appConfig[key] = this.appSetting[key]
          })
          this.modalConfirm = true
          console.log('appSetting:', appConfig)
        })
      },
      handleConfirmGenerate () {
        const appConfig = {}
        this.confirmGenerateLoadingFlag = true
        Object.keys(this.appSetting).map((key, index) => {
          appConfig[key] = this.appSetting[key]
        })
        appConfig['splashImage'] = appConfig['splashImage'][0] || null
        appConfig['level'] = this.appPackLevel
        this.$ajax.post('/apps/packing?timestamp=' + (new Date()).getTime(), appConfig).then((response) => {
          console.log('/apps/packing', response)
          this.appPackId = '11000000001'
          if (response && response.data && response.data.code === '200') {
            this.appPackId = response.data.data.appId
            this.appPackTestPage = `${location.origin}/download/preview/p/${parseInt(this.appPackId)}` // response.data.data.url
            this.generateProgressRunning()
            this.modalGenerate = true
            this.modalConfirm = false
            canvasQrcode.toDataURL(`${this.appPackTestPage || 'APP生成下载地址异常，请联系客服。(错误: 502)'}`, {}).then((data) => {
              this.modalGenerateData.qrcode = data
            })
//            window.onbeforeunload = () => {
//              return '提示：离开该页面您将不能再下载生成的APP，已经消耗的次数不会返还，确定要离开吗？'
//            }
          } else if (response && response.data && String(response.data.code) === '0') {
            this.$Message.error(response.data.msg)
            const _this = this
            setTimeout(() => {
              _this.confirmGenerateLoadingFlag = false
            }, 3000)
          } else {
            this.$Message.error('网络异常：生成APP操作失败！')
            const _this = this
            setTimeout(() => {
              _this.confirmGenerateLoadingFlag = false
            }, 3000)
          }
        }).catch(() => {
          this.$Message.error('网络异常：生成APP操作失败！')
          const _this = this
          setTimeout(() => {
            _this.confirmGenerateLoadingFlag = false
          }, 3000)
        })
      },
      handleDownloadPackage (deviceName) {
        const deviceNameToId = {
          androidButton: '1',
          iOSButton: '2'
        }
        const _this = this
        _this.modalGenerateData[deviceName].disable = true
        _this.modalGenerateData[deviceName].text = '正在下载'
        setTimeout(() => {
          _this.modalGenerateData[deviceName].disable = false
          _this.modalGenerateData[deviceName].text = '点击下载'
        }, 30000)
        const anchorDom = document.createElement('a')
        // https://api.dafu.im/apps/down?appId={appId}&device={1|2}
        anchorDom.href = `${this.$ajax.defaults.baseURL}/apps/down?appId=${this.appPackId}&device=${deviceNameToId[deviceName]}&token=${this.$ajax.defaults.headers.token}`
        anchorDom.target = '_blank'
        anchorDom.click()
      },
      closeGenerateApp () {
        console.log('closeGenerateApp')
        this.$Modal.confirm({
          content: '<h3 style="margin-bottom:10px; color: #f00">离开该页面您将不能再下载生成的APP，<br />已经消耗的次数不会返还，确定要离开吗？</h3>',
          okText: '返回下载',
          cancelText: '确认离开',
          onOk: () => {
            this.modalGenerate = true
            console.log('closeGenerateApp: OK')
          },
          onCancel: () => {
//            window.onbeforeunload = undefined
            this.$router.push('/pack_purchase')
            console.log('closeGenerateApp: Cancel')
          }
        })
        return false
      }
    }
  }
</script>

