              height: 24px;
              line-height: 24px;
              display: inline-block;
              color: #fff;
              position: absolute;
              text-align: center;
              cursor: pointer;
            }
            .color-picker {
              height: 0;
              overflow: hidden;
              transition: all 300ms ease-in-out;
            }
            &.show-picker .color-picker {
              height: 215px;
              padding-bottom: 20px;
            }
          }
        }
      }
    }
  }
</style>



