<template>
  <div class="sub-content invoice-detail">
    <h2>发票详情</h2>
    <div class="inner-box">
      <dl class="clear">
        <dt>订单号：</dt>
        <dd>{{invoiceInfo.id}}</dd>
      </dl>
      <dl class="clear">
        <dt>订单金额：</dt>
        <dd>{{parseInt(invoiceInfo.price, 10).toLocaleString()}} 元</dd>
      </dl>
      <dl class="clear">
        <dt>发票抬头：</dt>
        <dd>{{invoiceInfo.company}}</dd>
      </dl>
      <dl class="clear">
        <dt>税号：</dt>
        <dd>{{invoiceInfo.no}}</dd>
      </dl>
      <dl class="clear">
        <dt>收件地址：</dt>
        <dd>{{invoiceInfo.address}}</dd>
      </dl>
      <dl class="clear">
        <dt>收件人：</dt>
        <dd>{{invoiceInfo.name}}</dd>
      </dl>
      <dl class="clear">
        <dt>联系电话：</dt>
        <dd>{{invoiceInfo.phone}}</dd>
      </dl>
      <dl class="clear">
        <dt>发货状态：</dt>
        <dd>{{invoiceInfo.status}}</dd>
      </dl>
      <dl class="clear">
        <dt>快递公司：</dt>
        <dd>{{invoiceInfo.express}}</dd>
      </dl>
      <dl class="clear">
        <dt>快递单号：</dt>
        <dd>{{invoiceInfo.eid}}</dd>
      </dl>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageInvoiceDetail',
  props: {
    userInfo: Object
  },
  data () {
    return {
      invoiceInfo: {
        id: '12345',
        price: '2000',
        company: '腾讯科技（上海）有限公司',
        no: '0123456789',
        name: '殷伟',
        phone: '150*****010',
        address: '上海徐汇',
        status: '已发货',
        express: '顺丰快递',
        eid: '9876543210'
      }
    }
  },
  created () {
    const invoiceId = this.$route.params.id
    console.log(invoiceId)
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.invoice-detail {
  dt, dd {
    float: left;
    padding: 5px 16px;
  }
  dt {
    width: 10em;
    text-align: right;
  }
}
.invoice-box {
  margin: 20px 0;
}
</style>



