<template>
  <div class="content service">
    <div class="wrapper">
      <Row>
        <Col span="4">
        <Menu
          class="sidebar"
          theme="light"
          width="auto"
          :active-name="activeName"
          @on-select="handleSelect">
          <MenuGroup title="增值服务">
            <MenuItem
              v-for="item in menus"
              :key="item.id"
              :name="item.id">
              <span class="icon"><Icon :type="item.icon"/></span>
              {{item.name}}
            </MenuItem>
          </MenuGroup>
        </Menu>
        </Col>
        <Col span="20">
        <template>
          <router-view :user-info="userInfo"></router-view>
        </template>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageService',
  data () {
    return {
      activeName: '',
      menus: [
        {id: 'coupon', name: '优惠券', icon: 'pricetag'},
        {id: 'ad_data', name: '广告数据', icon: 'speakerphone'}
      ],
      userInfo: {}
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.userInfo = this.$store.userInfo
      this.activeName = this.$route.path.split('/')[2]
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    handleSelect (id) {
      this.$router.push(`/service/${id}`)
      this.activeName = id
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.service {
  .wrapper {
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .icon {
    display: inline-block;
    width: 16px;
    text-align: center;
  }
}
</style>



