    margin-bottom: 10px;
    font-size: 24px;
  }
  p {
    font-size: 20px;
  }
  .btn-group {
    width: 740px;
    margin: 32px auto;
    padding-top: 32px;
    border-top: 1px solid #eee;
    button {
      margin: 0 8px;
    }
  }
  .ad-area img {
