<template>
  <div class="content apps">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem>我的应用</BreadcrumbItem>
      </Breadcrumb>
      <Tabs value="apps" @on-click="handleChangeTab">
        <TabPane v-for="item in menus" :key="item.id" :label="item.name" :name="item.id"></TabPane>
      </Tabs>
      <div class="clear">
        <Button type="primary" @click="$router.push('/release')" icon="ios-upload" size="large">上传新应用</Button>
        <div class="search-box" @keyup.enter="handleSearch">
          <Input v-model="appName" placeholder="应用名称" style="width:200px"/><Button type="primary" @click="handleSearch">搜 索</Button>
        </div>
      </div>
      <Tabs type="card" v-model="tabIndex" @on-click="handleSearch">
        <TabPane v-for="item in appTabs" :icon="item.icon" :key="item.id" :label="item.name" :name="item.id"></TabPane>
      </Tabs>
      <div class="tab-content">
        <Table stripe :columns="columns" :data="list"></Table>
        <div class="pages">
          <Page
            v-if="total > 10"
            :total="total"
            show-total
            show-elevator
            @on-change="handlePageChange"/>
        </div>
      </div>
    </div>
    <Modal
      v-model="appsMerge.show"
      width="680px"
      :scrollable="true"
      title="选择 App"
      ok-text="合并应用"
      @on-cancel="appsMerge.choose = ''"
      @on-ok="handleMergeApps">
      <Alert>合并后的两个应用，进入任一个应用的单页，扫描二维码，会根据你的手机系统自动帮你下载相应的版本。</Alert>
      <ul class="merge-apps clear">
        <li
          v-for="item in appsMerge.list"
          :key="item.id"
          :class="[item.id === appsMerge.choose ? 'selected': '']"
          @click="appsMerge.choose = item.id">
          <img
            :src="`//pic.dafu.im/icons/${item.id}.png?${new Date(item.update_dt).getTime()}&imageView2/1/w/120/h/120//ignore-error/1`"
            alt=""
            height="120"
            width="120">
          <p>{{item.app_name}}</p>
          <p>{{item.package_name}}</p>
          <div class="selected-mask">
            <Icon type="checkmark-round" :size="60" color="#fff"/>
          </div>
        </li>
      </ul>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'PageApps',
  data () {
    return {
      menus: [
        {id: 'apps', name: '我的应用'},
        {id: 'apps_log', name: '下载日志'},
        {id: 'apps_list', name: '应用列表'}
      ],
      appName: '',
      tabIndex: 'whole',
      appTabs: [
        {id: 'whole', name: '全部'},
        {id: 'ipa', name: 'iOS', icon: 'social-apple'},
        {id: 'apk', name: 'Android', icon: 'social-android'}
      ],
      page: 1,
      total: 0,
      columns: [
        {title: 'ID', key: 'id', width: '100px', align: 'center'},
        {
          title: '类型',
          key: 'appType',
          width: '60px',
          align: 'center',
          render: (h, params) => {
            return h('Icon', {
              props: {
                type: params.row.ext === 'ipa' ? 'social-apple' : 'social-android',
                size: '28',
                color: params.row.ext === 'ipa' ? '#19be6b' : '#f90'
              }
            })
          }
        },
        {
          title: '图标',
          key: 'icon',
          align: 'center',
          render: (h, params) => {
            return h('img', {
              attrs: {
                src: `//pic.dafu.im/icons/${params.row.id}.png?${new Date(params.row.update_dt).getTime()}&imageView2/1/w/120/h/120//ignore-error/1`,
                height: 32,
                width: 32
              },
              style: {
                verticalAlign: 'top',
                borderRadius: '16%'
              }
            })
          }
        },
        {title: '名称', key: 'app_name', align: 'center'},
        {title: '备注', key: 'remark', align: 'center'},
        {title: '下载次数', key: 'download_num', align: 'center'},
        {
          title: '链接',
          key: 'url',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              attrs: {
                href: `${this.link}${params.row.url}`,
                target: '_blank'
              }
            }, params.row.url)
          }},
        {title: '更新时间', key: 'update_dt', align: 'center'},
        {
          title: '操作',
          key: 'handle',
          width: '405px',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning'
                },
                on: {
                  click: () => {
                    this.handleAppUpdate(params.row.id)
                  }
                }
              }, '升 级'),
              h('Button', {
                props: {
                  type: 'success'
                },
                on: {
                  click: () => {
                    this.handleAppMerge(params.row.id, params.row.ext)
                  }
                }
              }, '合并应用'),
              h('Button', {
                props: {
                  type: 'primary'
                },
                on: {
                  click: () => {
                    this.handleAppPreview(params.row.url)
                  }
                }
              }, '预 览'),
              h('Button', {
                props: {
                  type: 'info'
                },
                on: {
                  click: () => {
                    this.handleAppSet(params.row.id)
                  }
                }
              }, '设 置'),
              h('Button', {
                props: {
                  type: 'error'
                },
                on: {
                  click: () => {
                    this.handleAppDel(params.row.id, params.row.app_name)
                  }
                }
              }, '删 除')
            ])
          }
        }
      ],
      list: [],
      //  合并应用相关
      appsMerge: {
        show: false,
        id: '',
        choose: '',
        list: []
      }
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.handleSearch()
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    handleChangeTab (path) {
      this.$router.push(`/${path}`)
    },
    handleSearch () {
      const params = {
        page: this.page
      }
      if (this.appName) params.keyword = this.appName
      if (this.tabIndex !== 'whole') params.ext = this.tabIndex
      this.$ajax.get('/api/apps/pages', {params}).then((res) => {
        if (res.data && res.data.code === '200') {
          const data = res.data.data
          this.total = parseInt(data.pages.total, 10)
          this.list = data.apps
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handlePageChange (page) {
      this.page = page
      this.handleSearch()
    },
    handleAppUpdate (id) {
      this.$router.push(`/release?id=${id}`)
    },
    handleAppMerge (id, ext) {
      this.$ajax.get('/api/apps/pages', {params: {size: 20}}).then((res) => {
        if (res.data && res.data.code === '200') {
          const apps = res.data.data.apps
          const list = []
          apps.forEach((item) => {
            if (item.ext !== ext && item.merge === '0') list.push(item)
          })
          this.appsMerge.id = id
          this.appsMerge.list = list
          this.appsMerge.show = true
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handleAppPreview (url) {
      //  TODO 需要修改
      //  window.open(`${this.link}${url}`)
    },
    handleAppSet (id) {
      this.$router.push(`/apps_setting/${id}`)
    },
    handleAppDel (id, name) {
      this.$Modal.confirm({
        content: `你确定要删除APP【${name}】吗？`,
        onOk: () => {
          this.$ajax.get('/api/apps/del', {params: {id}}).then((res) => {
            if (res.data && res.data.code === '200') {
              const handleSearch = this.handleSearch
              this.$Message.success({
                content: '删除成功',
                duration: 1,
                onClose () {
                  handleSearch()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    },
    handleMergeApps () {
      this.$ajax.get('/api/apps/merge', {
        params: {
          id: this.appsMerge.id,
          choose: this.appsMerge.choose
        }
      }).then((res) => {
        this.appsMerge.choose = ''
        if (res.data && res.data.code === '200') {
          const handleSearch = this.handleSearch
          this.$Message.success({
            content: '合并成功',
            duration: 1,
            onClose () {
              handleSearch()
            }
          })
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.apps {
  .wrapper {
    overflow: hidden;
    .clear {
      margin-bottom: 20px;
    }
    button {
      margin-right:10px;
    }
    .search-box {
      float: right;
      button {
        margin: 0 0 0 10px;
      }
    }
  }
}
.tab-content {
  min-height: 468px;
  margin-top: -16px;
  padding: 16px;
  border: 1px solid #dddee1;
  border-top: 0;
  background: #fff;
  td button {
    margin: 0 5px;
  }
}
.pages {
  margin-top: 20px;
}
.merge-apps {
  max-height: 360px;
  overflow-y: auto;
  li {
    position: relative;
    float: left;
    width: 25%;
    margin: 10px 0;
    text-align: center;
    cursor: pointer;
    img {
      display: block;
      margin: 0 auto 10px;
    }
    p {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
.selected-mask {
  display: none;
}
.selected .selected-mask {
  display: block;
  position: absolute;
  top: 0;
  left: 21px;
  height: 120px;
  width: 120px;
  padding-top: 29px;
  background: rgba(0, 0, 0, .4);
  border-radius: 16px;
}
</style>



