<template>
  <div class="sub-content bind-qiniu">
    <h2>绑定七牛云</h2>
    <div class="inner-box">
      <Form
        ref="formBindQiniu"
        :model="formBindQiniu"
        :rules="ruleBindQiniu"
        :label-width="120">
        <FormItem label="Access Key：" prop="accesskey">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.accesskey" placeholder="请到七牛个人面板-密匙管理中获取"/>
        </FormItem>
        <FormItem label="Secret Key：" prop="secretkey">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.secretkey" placeholder="请到七牛个人面板-密匙管理中获取" />
        </FormItem>
        <FormItem label="空间名称：" prop="bucket">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.bucket" placeholder="请到七牛资源首页-对象储存中获取空间存储区域必须选择华东" />
        </FormItem>
        <FormItem label="七牛空间域名：" prop="domain">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.domain" placeholder="请到七牛资源首页-对象储存中-测试域名中获取-直接复制过来" />
        </FormItem>
        <FormItem>
          <Button
            v-if="userInfo.user_type"
            :disabled="userInfo.user_type === '1'"
            type="primary"
            @click="handleSubmit('formBindQiniu')">绑 定</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageBindQiniu',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formBindQiniu: {
        accesskey: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.accesskey,
        secretkey: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.secretkey,
        bucket: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.bucket,
        domain: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.domain
      },
      ruleBindQiniu: {
        accesskey: [
          {required: true, message: 'Access Key 不能为空', trigger: 'click'}
        ],
        secretkey: [
          {required: true, message: 'Secret Key 不能为空', trigger: 'click'}
        ],
        bucket: [
          {required: true, message: '空间名称不能为空', trigger: 'click'}
        ],
        domain: [
          {required: true, message: '域名不能为空', trigger: 'click'}
        ]
      }
    }
  },
  created () {
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$ajax.post('/api/userInfo/bindQiNiu', this.formBindQiniu).then((res) => {
            if (res.data && res.data.code === '200' && res.data.data.ret === '1') {
              this.$Message.success({
                content: '七牛云绑定成功',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



