<template>
  <div class="content price">
    <div class="wrapper">
      <div class="inner-box">
        <h2>选择下载次数包</h2>
        <div class="step-1 clear">
          <Card
            :class="['goods-item', formSubmit.goods_id === key ? 'active' : '']"
            v-for="(value, key) in goods"
            :key="key"
            :padding="0">
            <h3><i>￥</i>{{value.price}}</h3>
            <div @click="handleSelect(key, value.price)">
              <p>累计下载次数: </p>
              <p>{{value.desc}}</p>
              <p><Button
                type="ghost"
                size="large"
                shape="circle"
                style="width: 100px">选 择</Button></p>
            </div>
          </Card>
        </div>
        <h2>选择支付方式</h2>
        <div class="step-2 clear">
          <Card
            :class="['pay-item', formSubmit.channel === item.id ? 'active' : '']"
            v-for="item in payTypes"
            :key="item.id">
            <p @click="handleSelect(item.id)"><img :src="item.img" :alt="item.name"></p>
          </Card>
        </div>
        <h2>总计：<span style="color: #ed3f14">￥{{price}}</span> 元
          <Button
            type="primary"
            size="large"
            shape="circle"
            style="width: 120px"
            :disabled="!(this.formSubmit.goods_id && this.formSubmit.channel)"
            @click="handleSubmit">去支付</Button></h2>
      </div>
    </div>
  </div>
</template>

<script>
import alipay from '../assets/images/alipay.png'
import weixinpay from '../assets/images/weixinpay.png'
import unionpay from '../assets/images/unionpay.png'

export default {
  name: 'PagePrice',
  data () {
    return {
      disabled: true,
      goods: {},
      payTypes: [
        {name: '支付宝', id: 'alipay', img: alipay},
        {name: '微信支付', id: 'weixin', img: weixinpay},
        {name: '银联支付', id: 'union', img: unionpay}
      ],
      price: this.$route.query.price || 0,
      formSubmit: {
        type: '100000',
        goods_id: this.$route.query.goods_id || '100002',
        channel: this.$route.query.channel || 'alipay'
      }
    }
  },
  created () {
    this.$ajax.get('/api/payment/goods?type=100000').then((res) => {
      if (res.data && res.data.code === '200') {
        this.goods = res.data.data.goods
        this.price = this.price || this.goods['100002'].price
      } else {
        this.$Message.error(res.data.msg)
      }
    }).catch((err) => {
      console.log(err)
    })
  },
  methods: {
    handleSelect (id, price) {
      if (arguments.length === 2) {
        this.formSubmit.goods_id = id
        this.price = price
      }
      if (arguments.length === 1) {
        this.formSubmit.channel = id
      }
    },
    handleSubmit () {
      if (window.localStorage.getItem('isLogin')) {
        //  异步请求前创建新窗口，不然会被 chrome 拦截
        const newWindow = window.open('about:blank')
        //  创建订单
        this.$ajax.post('/api/payment/center', this.formSubmit).then((res) => {
          if (res.data && res.data.code === '200') {
            const data = res.data.data
            let url = 'about:blank'
            if (data.channel === 'weixin') {
              //  需要和当前路由模式匹配，当前为 hash 模式
              url = `#/pay_weixin?qr_code=${encodeURIComponent(data.url)}`
            } else {
              url = data.url
            }
            newWindow.location = url
            this.$Modal.confirm({
              content: '<h3 style="margin-bottom:10px">请在新开窗口完成支付</h3><p style="color:#999">并根据您的支付情况完成以下操作</p>',
              okText: '完成支付',
              cancelText: '支付遇到问题？',
              onOk: this.handleOk.bind(this, data.trade_id),
              onCancel: this.handleCancel
            })
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
      } else {
        const formSubmit = this.formSubmit
        const price = this.price
        const router = this.$router
        this.$Message.info({
          content: '请登录后继续下一步操作，即将跳转到登录页面...',
          duration: 3,
          onClose () {
            const path = `/login#price?goods_id=${formSubmit.goods_id}&channel=${formSubmit.channel}&price=${price}`
            router.push(path)
          }
        })
      }
    },
    handleOk (id) {
      this.$ajax.get(`/api/payment/tradeStatus?id=${id}`).then((res) => {
        if (res.data && res.data.code === '200') {
          if (res.data.data.trade.status === '1') {
            const router = this.$router
            this.$Message.success({
              content: '购买成功！即将跳转...',
              duration: 3,
              onClose () {
                router.replace('/apps')
                setTimeout(() => {
                  window.location.reload()
                }, 0)
              }
            })
          } else {
            this.$Message.error({
              content: '购买未成功，请重新尝试...',
              duration: 3,
              onClose () {
                window.location.reload()
              }
            })
          }
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handleCancel () {
      console.log('cancel')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.inner-box {
  width: 960px;
  margin: auto;
  h2 {
    margin-bottom: 20px;
    font-weight: normal;
    font-size: 24px;
  }
}
.step-1, .step-2 {
  margin-bottom: 30px;
  cursor: pointer;
}
.goods-item {
  float: left;
  width: 280px;
  margin: 0 15px;
  text-align: center;
  h3 {
    padding-top: 45px;
    height: 140px;
    border-radius: 0 0 50% 50%;
    font-weight: normal;
    font-size: 46px;
    line-height: 46px;
    color: #fff;
    background: #2d8cf0;
    i {
      margin-right: 10px;
      font-size: 22px;
      font-style: normal;
    }
  }
  div {
    padding: 25px 0;
    p {
      margin: 10px 0;
      font-size: 20px;
    }
  }
}
.ivu-card-bordered.active {
  border-color: #2d8cf0;
  .ivu-btn-ghost {
    color: #fff;
    background: #2d8cf0;
    border-color: #2d8cf0;
  }
}
.pay-item {
  float: left;
  width: 280px;
  margin: 0 15px;
  text-align: center;
  img {
    display: block;
    width: 100%;
  }
}
</style>



