<template>
  <div class="pack">
    <div class="banner" style="position: relative">
      <img src="../assets/images/banner_pack.jpg" alt="">
      <div class="now-start-pack" style="width: 9%; height: 10%; top: 48.5%; left: 80.25%; z-index: 30; position: absolute;">
        <Button size="large" style="width: 100%; height: 100%; font-size: calc(1vw)" @click="handleGotoPackPurchase">立即制作</Button>
      </div>
    </div>
    <div class="service-intro-1">
      <img src="../assets/images/bg_pack_0.jpg" alt="">
      <div class="inner-box">
        <h3>WAP转APP-初级版本</h3>
        <p>只需要提供网站链接，APP图标、APP名字、启动页</p>
        <p>3分钟即可封装成一个APP</p>
        <p>支持安卓和苹果系统</p>
        <p>自适应横竖屏</p>
        <p>无广告、无后门</p>
        <p><Button type="primary" size="large" style="width: 160px" @click="handleGotoPackPurchase">立即制作</Button></p>
      </div>
    </div>
    <div class="service-intro-2">
      <h3>WAP转APP-高级版</h3>
      <p><Button type="primary" size="large" style="width: 160px" @click="handleGotoPackPurchase">立即制作</Button></p>
      <div class="inner-box">
        <Row>
          <Col span="6">
          <img src="../assets/images/icon_pack_0.png" alt="">
          <h4>各类导航</h4>
          <p>支持2~5栏导航菜单在线更换<br/>
            可更换名称，图标，导航、链接等</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_pack_1.png" alt="">
          <h4>消息推送</h4>
          <p>消息推送系统，秒发20万条以上<br/>
            独立的后台编写推送内容</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_pack_2.png" alt="">
          <h4>社交分享</h4>
          <p>分享到QQ空间、微信朋友圈<br/>
            微信好友、QQ空间、新浪微博等</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_pack_3.png" alt="">
          <h4>数据统计</h4>
          <p>专业的移动APP数据统计分析平台<br/>
            分析流量来源、使用功能数据等</p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-3">
      <div class="inner-box" :style="`background:url(${imgs[0]}) no-repeat 5% 50%/28.6% auto`">
        <Row>
          <Col span="16" offset="8">
          <h3>纯原生代码编写</h3>
          <p>权限完全可控，运行速度快，无后门， 支持安卓和苹果系统</p>
          <p>直接调用系统API，使用多线程，缓存技术，性能稳定，访问本地资源，运行速度快、流畅</p>
          </Col>
        </Row>
      </div>
      <div class="inner-box text-right" :style="`background:url(${imgs[1]}) no-repeat 80% 50%/36.19% auto`">
        <Row>
          <Col span="12">
          <h3>永久使用无续费</h3>
          <p>一次性收费，源码修改权自己控制，终身维护，个性化需求单独购买</p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-4">
      <h3>可上架苹果安卓市场</h3>
      <p>苹果版可上架App Store，支持企业签名，安卓版可上架到任何应用市场，包上架到成功</p>
      <div class="inner-box">
        <Row>
          <Col span="6">
          <img src="../assets/images/icon_pack_4.png" alt="">
          <p>各大安卓市场</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_pack_5.png" alt="">
          <p>AppStore</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_pack_6.png" alt="">
          <p>企业签名</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_pack_7.png" alt="">
          <p>大福平台</p>
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
import bg1 from '../assets/images/bg_pack_1.png'
import bg2 from '../assets/images/bg_pack_2.png'

export default {
  name: 'PagePack',
  data () {
    return {
      imgs: [bg1, bg2]
    }
  },
  created () {
    this.$ajax.get('user/my-services?type=1', {type: 1}).then((response) => {
      console.log('user/my-services', response)
      if (response.data && String(response.data.code) === '200') {
        const userPurchaseInfo = response.data.data
        if (userPurchaseInfo.level && userPurchaseInfo.limited_nums) {
          this.$router.push('/user/pack/list')
        }
      }
    })
  },
  methods: {
    handleGotoPackPurchase () {
      this.$router.push(`/pack_purchase`)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner img {
  display: block;
  width: 100%;
  margin: auto;
}
.service-intro-1 {
  position: relative;
  img {
    display: block;
    width: 100%;
    margin: auto;
  }
  .inner-box {
    position: absolute;
    top: 20%;
    left: 55%;
  }
  h3 {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 25px;
    font-weight: normal;
  }
  p {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 16px;
  }
}
.service-intro-2 {
  padding: 45px 0;
  text-align: center;
  .inner-box {
    width: 70%;
    margin: 30px auto 0;
  }
  h3 {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 25px;
    font-weight: normal;
  }
  h4 {
    margin: 20px 0;
    font-size: 22px;
    line-height: 25px;
    font-weight: normal;
  }
  p {
    font-size: 16px;
  }
}
.service-intro-3 {
  padding: 45px 0;
  background: #fff;
  .inner-box {
    width: 70%;
    max-width: 1903px;
    margin: auto;
    padding: 120px 0 110px;
    border-bottom: 1px solid #eee;
  }
  h3 {
    margin: 0 20px 30px;
    font-size: 24px;
    line-height: 25px;
    font-weight: normal;
  }
  p {
    margin: 10px 20px;
    font-size: 16px;
  }
  .text-right {
    padding: 140px 0;
    text-align: right;
  }
}
.service-intro-4 {
  padding-bottom: 45px;
  background: #fff;
  text-align: center;
  h3 {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 25px;
    font-weight: normal;
  }
  p {
    margin-bottom: 50px;
    font-size: 16px;
  }
  .inner-box {
    width: 70%;
    margin: auto;
    p {
      margin: 10px 0 0;
    }
  }
}
</style>



