          '<p>2.2 您声明并保证，在本服务协议的有效期内，您拥有（并将一直拥有）您上传、存储到服务或通过服务共享的您的内容的所有必要权利，并且我们收集、使用和保留您的内容不会违反任何法律和他人的权利。 dafu.im 不会拥有、控制、验证、认可您的内容，也不会为您的内容付费或者以其他方式对您的内容承担任何责任，并且， dafu.im 不会对他人使用我们的服务上传、存储或共享的您的内容或材料承担任何责任。</p>',
          '<p>2.3 在为您或他人提供服务、保护您和服务的安全以及改进 dafu.im 产品和服务所必需的范围内，您授予 dafu.im 使用您的内容的权利。</p>',
          '<h3>3. 关于Cookie</h3>',
          '<p>当您访问 dafu.im 或其服务时， dafu.im 可能会以 "Cookie" 的形式将某些信息存入您的计算机，"Cookie" 是网页服务器放置在您的计算机上的一个小的文本文件，"Cookie"不能用于运行程序，也不会将病毒传播到您的计算机上。使用 "Cookie" 的目的是为您提供一项节省时间的简便功能，但并不表示 dafu.im 可自动获悉有关您的任何个人信息。您可以选择接受或拒绝 "Cookie"。</p>',
          '<h3>4. 站内链接</h3>',
          '<p> dafu.im 可能包含与其他站点的链接， dafu.im 不对其他站点的隐私条约或内容负责。</p>',
          '<h3>5. 修改</h3>',
          '<p> dafu.im 可能会不时修改我们的隐私政策。这些修改会反映在本政策中。任何修改都会将您的满意度置於首位。我们鼓励您在每次访问 dafu.im 和 dafu.im 的其他服务时都查阅我们的隐私政策。</p>'
        ].join('')
      }
    }
  }
}
