// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import iView from 'iview'
import { getBaseUrl, getUserToken } from './utils/GetAxiosBaseValue'
import 'iview/dist/styles/iview.css'

axios.defaults.withCredentials = true //  TODO 测试时跨域设置，后期可以删除
axios.defaults.baseURL = getBaseUrl() //  TODO 根据URL获取对应的API服务地址
axios.defaults.headers = getUserToken() //  TODO 配置当前用户的Token请求头

Vue.prototype.$ajax = axios

Vue.prototype.$store = {
  isLogin: false,
  userInfo: {}
}

Vue.use(iView)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router: router,
  template: '<App/>',
  components: { App }
})



