<template>
  <div class="float-bar">
    <div
      class="float-bar-item"
      @click="openWindow('http://wpa.qq.com/msgrd?v=3&uin=3261682395&site=qq&menu=yes')">
      <img class="icon" src="../assets/images/icon_qq.png" alt=""><img class="icon-hover" src="../assets/images/icon_qq_hover.png" alt="">签名客服</div>
    <div
      class="float-bar-item"
      @click="openWindow('http://wpa.qq.com/msgrd?v=3&uin=1455653215&site=qq&menu=yes')">
      <img class="icon" src="../assets/images/icon_qq.png" alt=""><img class="icon-hover" src="../assets/images/icon_qq_hover.png" alt="">托管客服</div>
    <div v-if="backTop" class="float-bar-item" @click="back">
      <img class="icon" src="../assets/images/icon_top.png" alt=""><img class="icon-hover" src="../assets/images/icon_top_hover.png" alt="">回到顶部</div>
  </div>
</template>

<script>
export default {
  name: 'FloatBar',
  data () {
    return {
      backTop: false
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll, false)
    window.addEventListener('resize', this.handleScroll, false)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.handleScroll, false)
    window.removeEventListener('resize', this.handleScroll, false)
  },
  methods: {
    handleScroll () {
      //  页面滚动高度达到某值出现
      this.backTop = window.pageYOffset >= 400
    },
    back () {
      const sTop = document.documentElement.scrollTop || document.body.scrollTop
      this.scrollTop(window, sTop, 0)
    },
    openWindow (url) {
      window.open(url)
    },
    scrollTop (el, from = 0, to, duration = 500) {
      if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = (
          window.webkitRequestAnimationFrame ||
          window.mozRequestAnimationFrame ||
          window.msRequestAnimationFrame ||
          function (callback) {
            return window.setTimeout(callback, 1000 / 60)
          }
        )
      }
      const difference = Math.abs(from - to)
      const step = Math.ceil(difference / duration * 50)
      function scroll (start, end, step) {
        if (start === end) return

        let d = (start + step > end) ? end : start + step
        if (start > end) {
          d = (start - step < end) ? end : start - step
        }

        if (el === window) {
          window.scrollTo(d, d)
        } else {
          el.scrollTop = d
        }
        window.requestAnimationFrame(() => scroll(d, end, step))
      }
      scroll(from, to, step)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.float-bar {
  position: fixed;
  z-index: 999;
  top: 50%;
  right: 1%;
}
.float-bar-item {
  height: 72px;
  width: 72px;
  padding: 10px 0;
  border: 1px solid #ddd;
  border-top-width: 0;
  text-align: center;
  background: #fff;
  cursor: pointer;
  .icon {
    display: block;
    margin: 0 auto 5px;
  }
  .icon-hover {
    display: none;
  }
}
.float-bar-item:first-child {
  border-top-width: 1px;
}
.float-bar-item:hover {
  color: #fff;
  background: #2d8cf0;
  border-color: #0b67e4;
  .icon {
    display: none;
  }
  .icon-hover {
    display: block;
    margin: 0 auto 5px;
  }
}
</style>



