<template>
  <div class="sub-content modify-pwd">
    <h2>修改密码</h2>
    <div class="form-box">
      <Form
        ref="formModifyPwd"
        :model="formModifyPwd"
        :rules="ruleModifyPwd">
        <FormItem prop="oldPassword">
          <Input
            type="password"
            v-model="formModifyPwd.oldPassword"
            placeholder="请输入当前密码，不少于6个字符">
          <Icon
            type="android-unlock"
            size="16"
            slot="prepend"/>
          </Input>
        </FormItem>
        <FormItem prop="password">
          <Input
            type="password"
            v-model="formModifyPwd.password"
            placeholder="请输入新密码，不少于6个字符">
          <Icon
            type="android-unlock"
            size="16"
            slot="prepend"/>
          </Input>
        </FormItem>
        <FormItem prop="confirmPassword">
          <Input
            type="password"
            v-model="formModifyPwd.confirmPassword"
            placeholder="请再次输入新密码">
          <Icon
            type="android-unlock"
            size="16"
            slot="prepend"/>
          </Input>
        </FormItem>
        <FormItem>
          <Button
            type="primary"
            size="large"
            shape="circle"
            icon="ios-compose-outline"
            long
            @click="handleSubmit('formModifyPwd')">修 改</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageModifyPwd',
  data () {
    return {
      formModifyPwd: {
        oldPassword: '',
        password: '',
        confirmPassword: ''
      },
      ruleModifyPwd: {
        oldPassword: [
          {required: true, message: '密码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '密码不能少于6个字符', trigger: 'click'}
        ],
        password: [
          {required: true, message: '密码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '密码不能少于6个字符', trigger: 'click'}
        ],
        confirmPassword: [
          {validator: this.validatePassCheck, trigger: 'click'}
        ]
      }
    }
  },
  methods: {
    validatePassCheck (rule, value, callback) {
      if (value === '') {
        callback(new Error('确认密码不能为空'))
      } else if (value !== this.formModifyPwd.password) {
        callback(new Error('密码两次输入不一致'))
      } else {
        callback()
      }
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const formModifyPwd = {
            oldpassword: this.formModifyPwd.oldPassword,
            password: this.formModifyPwd.password
          }
          this.$ajax.post('/api/userInfo/password', formModifyPwd).then((res) => {
            if (res.data && res.data.code === '200') {
              this.$Message.success('密码修改成功')
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.form-box {
  width: 380px;
  margin: auto;
}
</style>



