<template>
  <div class="sub-content invoice">
    <h2>发票管理</h2>
    <div class="inner-box">
      <p style="margin-bottom:10px;text-align:right"><Button type="primary" @click="handleApply">申请发票</Button></p>
      <Alert>发货提示：发票将在每周的周五准时发出，请耐心等待。</Alert>
      <div class="invoice-box">
        <Table stripe :columns="columns" :data="list"></Table>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageInvoice',
  props: {
    userInfo: Object
  },
  data () {
    return {
      page: 1,
      total: 0,
      columns: [
        {title: '订单号', key: '1'},
        {title: '发票抬头', key: '2'},
        {title: '发票金额', key: '3'},
        {title: '申请时间', key: '4'},
        {title: '收件人姓名', key: '5'},
        {title: '状态', key: '6'},
        {title: '发货时间', key: '7'},
        {title: '快递公司', key: '8'},
        {title: '快递单号', key: '9'},
        {
          title: '详情',
          key: '10',
          render: (h, params) => {
            return h('Button', {
              props: {
                type: 'primary'
              },
              on: {
                click: () => {
                  this.handleWatchDetail(params.index)
                }
              }
            }, '查看详情')
          }
        }
      ],
      list: []
    }
  },
  created () {
  },
  methods: {
    handlePageChange (page) {
      this.page = page
    },
    handleApply () {
    },
    handleWatchDetail (index) {
      // TODO
      console.log(index)
      this.$router.push(`/invoice_detail/${index}`)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.search-box {
  span {
    margin: 0 5px;
  }
  button {
    margin-left: 10px;
  }
}
.invoice-box {
  margin: 20px 0;
}
</style>



