<template>
  <div class="sub-content my-pack">
    <h2>我的封装</h2>
    <div class="inner-box">
      <div class="search-box" @keyup.enter="handleSearch">
        <DatePicker
          type="date"
          placeholder="选择开始日期"
          style="width: 200px"
          @on-change="handleStartChange"/>
        <span>至</span>
        <DatePicker
          type="date"
          placeholder="选择结束日期"
          style="width: 200px"
          @on-change="handleEndChange"/>
        <Button type="primary" @click="handleSearch">搜 索</Button>
      </div>
      <div class="my-pack-box">
        <Table stripe :columns="columns" :data="list"></Table>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
    </div>
  </div>
</template>

<script>
import formatDate from '../utils/FormatDate'

export default {
  name: 'PageMyPack',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formSearch: {
        start_dt: '',
        end_dt: '',
        page: 1
      },
      total: 0,
      columns: [
        {title: 'APP名称', key: '1'},
        {title: '客户端类型', key: '2'},
        {title: '封装时间', key: '3'},
        {title: '封装类型', key: '4'},
        {
          title: '原始包',
          key: '5',
          render: (h, params) => {
            return h('Button', {
              props: {
                type: 'primary'
              },
              on: {
                click: () => {
                  this.handleDownload(params.index)
                }
              }
            }, '下 载')
          }
        }
      ],
      list: []
    }
  },
  created () {
    //  初始赋值为今天开始往前一周
    const today = new Date().getTime()
    this.formSearch.end_dt = formatDate(today)
    this.formSearch.start_dt = formatDate(today - 6 * 24 * 3600 * 1000)
    this.handleSearch()
  },
  methods: {
    handleStartChange (value) {
      this.formSearch.start_dt = value
    },
    handleEndChange (value) {
      this.formSearch.end_dt = value
    },
    handlePageChange (page) {
      this.formSearch.page = page
      this.handleSearch()
    },
    handleSearch () {
      const start = new Date(this.formSearch.start_dt).getTime()
      const end = new Date(this.formSearch.end_dt).getTime()
      if (start > end) {
        this.$Message.error('开始日期不能晚于结束日期')
      } else {
        /*
        this.$ajax.post('/api/userTrade/get', this.formSearch).then((res) => {
          if (res.data && res.data.code === '200') {
            const pages = res.data.data.pages
            const rows = res.data.data.rows
            this.total = parseInt(pages.total, 10)
            if (rows && rows.length) {
              for (let i = 0, l = rows.length; i < l; i++) {
                this.$set(this.payList, i, {
                  id: rows[i].id,
                  update_dt: rows[i].update_dt,
                  goods: `${rows[i].goods.title} ${rows[i].goods.desc}`,
                  price: rows[i].price,
                  status: rows[i].status,
                  invoice: ''
                })
              }
            } else {
              this.payList = []
            }
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
        */
      }
    },
    handleDownload (index) {
      // TODO
      console.log(index)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.search-box {
  span {
    margin: 0 5px;
  }
  button {
    margin-left: 10px;
  }
}
.my-pack-box {
  margin: 20px 0;
}
</style>



