<template>
  <div class="content pack-purchase">
    <div class="purchase-content">
      <ul class="prices">
        <li>
          <span>files:</span>
          <input type="file" accept="image/png,image/jpeg" @change="handleFileUpload1" />
          <div>
            <img v-bind:src="image1" />
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import GetFileImageBase64 from '../utils/GetFileImageBase64'

  export default {
    name: 'PagePackResult',
    data () {
      return {
        image1: ''
      }
    },
    created () {
    },
    methods: {
      handleFileUpload1 (event) {
        const _this = this
        GetFileImageBase64(event.target).then((imageDataList) => {
          console.log('imageDataList: ', imageDataList, _this)
          _this.image1 = imageDataList[0]
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .pack-purchase {
    img {
      margin-top: 30px;
      max-width: 200px;
    }
  }
</style>



