<template>
  <div class="content app-setting">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem to="/apps">我的应用</BreadcrumbItem>
        <BreadcrumbItem>应用设置</BreadcrumbItem>
      </Breadcrumb>
      <Card :padding="45" shadow>
        <div class="inner-box">
          <div class="app-icon">
            <div class="icon">
              <Upload
                ref="upload"
                :action="`//dafu.im/api/apps/icon`"
                name="icon"
                :data="{id}"
                :with-credentials="true"
                accept=".png"
                :format="['png']"
                :before-upload="handleBeforeUpload"
                :on-success="handleUploadSuccess"
                :on-error="handleUploadError">
                <div class="upload-btn">
                  <Icon type="ios-cloud-upload" size="52"></Icon>
                  <p>更换图标</p>
                </div>
              </Upload>
              <img
                ref="icon"
                :src="`//pic.dafu.im/icons/${id}.png?${new Date(appData.update_dt).getTime()}&imageView2/1/w/120/h/120//ignore-error/1`"
                height="120"
                width="120"
                alt="">
            </div>
            <div class="app-info">
              <p><span>类型：{{appData.ext === 'ipa' ? 'iOS' : 'Android'}}</span><span>包名：{{appData.package_name}}</span></p>
              <p><span>版本：{{appData.version}}</span><span>大小：{{(appData.app_size / 1000000).toFixed(2)}}M</span><span>下载数量：{{appData.download_num}}</span></p>
              <Button type="primary" shape="circle" size="small" style="width:7em" @click="handleUploadBtnClick">更换图标</Button>
            </div>
            <div class="app-btn">
              <p><Button type="primary" icon="upload" size="large" style="width:120px" @click="handleUpgrade">上传新版本</Button></p>
              <p><Button type="ghost" icon="eye" size="large" style="width:120px" @click="handlePreviewTemplate">预览</Button></p>
            </div>
            <div class="app-alert clear">
              <Alert type="warning" show-icon style="float:left">提供iOS企业签名服务，支持按月/季度/半年/全年，长久稳定。请联系客服<a
                href="http://wpa.qq.com/msgrd?v=3&amp;uin=3261682395&amp;site=qq&amp;menu=yes" target="_blank">
                QQ：3261682395</a></Alert>
              <Alert v-if="violation" type="error" show-icon style="float:right">
                <Icon type="android-warning" slot="icon"></Icon>
                <template slot="desc"><span style="font-size:16px">该APP违反平台规则，请尽快删除</span></template>
              </Alert>
            </div>
          </div>
          <Tabs type="card" value="base">
            <TabPane label="基本设置" name="base">
              <Form
                ref="formSetting"
                :model="formSetting"
                :rules="ruleSetting"
                :label-width="120">
                <FormItem prop="app_name" label="APP名称">
                  <Input
                    type="text"
                    v-model="formSetting.app_name"
                    placeholder="请输入APP名称"
                    style="width:400px;margin-right:10px" />
                  <span>支持修改APP名称</span>
                </FormItem>
                <FormItem prop="url" label="短链接">
                  <Input
                    type="text"
                    v-model="formSetting.url"
                    placeholder="请输入6位字母数字"
                    style="width:400px;margin-right:10px">
                  <span slot="prepend">https://dafu.im/</span></Input>
                  <span style="position:absolute;left:410px;top:0">支持自定义修改，6位字母数字组合</span>
                </FormItem>
                <FormItem label="联系方式">
                  <Input
                    type="text"
                    v-model="formSetting.contact"
                    placeholder="可填写微信/QQ/电话/手机等"
                    style="width:400px;margin-right:10px" />
                  <span>方便第一时间联系用户，及时解决用户疑问</span>
                </FormItem>
                <FormItem label="下载密码">
                  <Input
                    type="text"
                    v-model="formSetting.password"
                    style="width:400px;margin-right:10px" />
                  <span>设置密码后，用户下载需要输入密码</span>
                </FormItem>
                <FormItem label="下载数量限制">
                  <Input
                    type="text"
                    v-model="formSetting.limit_num"
                    placeholder="请输入下载数量限制"
                    style="width:400px;margin-right:10px" />
                  <span>[0为不限制]，下载数量超过限制后该APP无法下载</span>
                </FormItem>
                <template v-if="appData.ext === 'ipa'">
                  <FormItem label="苹果商店地址">
                    <Input
                      type="text"
                      v-model="formSetting.appstore_url"
                      placeholder="请输入苹果店铺地址"
                      style="width:330px;margin-right:10px"
                      :disabled="appStoreUrlIsDisabled" /><Button type="primary" style="margin-right:10px" @click="appStoreUrlIsDisabled = false">修 改</Button><span>
                填写苹果商店地址后，直接跳转到App Store</span>
                  </FormItem>
                </template>
                <FormItem label="APP简介">
                  <Input
                    type="textarea"
                    v-model="formSetting.app_intro"
                    placeholder="可以填写产品介绍，更新介绍，玩法介绍等"
                    style="width:400px" />
                </FormItem>
                <FormItem label="备注">
                  <Input
                    type="text"
                    v-model="formSetting.remark"
                    placeholder="请输入备注"
                    style="width:400px;margin-right:10px" />
                  <span>用于APP区分说明，不显示</span>
                </FormItem>
                <FormItem>
                  <Button type="primary" shape="circle" style="width:120px;margin-right:10px" @click="handleSubmit('formSetting')">保 存</Button>
                </FormItem>
              </Form>
            </TabPane>
            <TabPane label="模板选择" name="template">
              <ul class="template-list clear">
                <li
                  v-for="item in templates"
                  :key="item.id"
                  :class="[item.id === selected ? 'selected': '']"
                  @click="selected = item.id">
                  <img :src="item.img" alt="">
                  <span class="checkbox"><Icon type="checkmark" :size="28" color="#fff"/></span>
                </li>
              </ul>
              <Alert>提示：如果需要定制模板，请联系客服，询问定制模板需求。QQ: <a href="http://wpa.qq.com/msgrd?v=3&uin=3261682395&site=qq&menu=yes" target="_blank">32-6168-2395</a></Alert>
              <Button type="primary" shape="circle" style="width:120px;margin-right:10px" @click="handleSubmitTemplate">保 存
              </Button><Button type="ghost" shape="circle" style="width:120px;margin-right:10px" @click="handlePreviewTemplate">预 览</Button>
            </TabPane>
          </Tabs>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import tpl0 from '../assets/template/images/default.png'
import tpl1 from '../assets/template/images/qipai2.jpg'
import tpl2 from '../assets/template/images/majiang.jpg'
import tpl3 from '../assets/template/images/buyu.jpg'

export default {
  name: 'PageAppsSetting',
  data () {
    return {
      id: '',
      violation: false,
      appData: {},
      appStoreUrlIsDisabled: true,
      uploadList: [],
      formSetting: {},
      ruleSetting: {
        app_name: [
          {required: true, message: 'App名称不能为空', trigger: 'click'}
        ],
        url: [
          {required: true, message: '短链接不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '短链接不能少于6个字符', trigger: 'click'}
        ]
      },
      selected: '',
      //  模板列表和静态目录一一对应
      templates: [
        {id: 'default', img: tpl0},
        {id: 'qipai2', img: tpl1},
        {id: 'majiang', img: tpl2},
        {id: 'buyu', img: tpl3}
      ]
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.id = this.$route.params.id
      this.$ajax.get('/api/apps/detail', {params: {id: this.id}}).then((res) => {
        if (res.data && res.data.code === '200') {
          this.appData = res.data.data
          this.selected = res.data.data.template || 'default'
          const {app_name, app_intro, password, url, appstore_url, contact, limit_num, remark} = res.data.data
          this.formSetting = {app_name, app_intro, password, url, appstore_url, contact, limit_num, remark}
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  mounted () {
    this.uploadList = this.$refs.upload.fileList
  },
  methods: {
    handleUpgrade () {
      this.$router.push(`/release?id=${this.id}`)
    },
    //  限制上传文件数目
    handleBeforeUpload () {
      const check = this.uploadList.length < 1
      if (!check) {
        this.$Message.warning('同时只能上传 1 张图片作为图标')
      }
      return check
    },
    handleUploadSuccess () {
      this.$refs.icon.setAttribute('src', `//pic.dafu.im/icons/${this.id}.png?${new Date().getTime()}&imageView2/1/w/120/h/120//ignore-error/1`)
      this.$refs.upload.fileList = []
      this.uploadList = []
    },
    handleUploadError (err) {
      this.$Message.error(err)
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.formSetting.id = this.id
          this.$ajax.post('/api/apps/update', this.formSetting).then((res) => {
            if (res.data && res.data.code === '200') {
              this.$Message.success({
                content: '设置修改成功',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    },
    handleSubmitTemplate () {
      this.$ajax.post('/api/apps/template', {
        id: this.id,
        template: this.selected
      }).then((res) => {
        if (res.data && res.data.code === '200') {
          this.$Message.success('模板修改成功')
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handlePreviewTemplate () {
      // window.open(`/static/template.html?code=${this.selected}`)
    },
    handleUploadBtnClick () {
      this.$refs.upload.handleClick()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.app-setting {
  .app-icon {
    margin-bottom: 20px;
    border-bottom: 1px solid #f5f8fb;
  }
  .icon {
    position: relative;
    float: left;
    height: 120px;
    width: 120px;
    margin: 0 20px 20px 0;
    img {
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
    }
  }
  .app-info {
    float: left;
    margin-top: 5px;
    > p {
      margin-bottom: 20px;
      > span {
        display: inline-block;
        width: 150px;
        margin-right: 10px;
      }
    }
  }
  .app-btn {
    float: right;
    p {
      margin-bottom: 10px;
    }
  }
  .app-alert {
    clear: both;
    margin-bottom: 10px;
  }
  .ivu-upload {
    float: left;
    margin-right: 10px;
  }
}
.template-list {
  margin-bottom: 20px;
  li {
    position: relative;
    float: left;
    padding: 5px;
    max-width: 25%;
    border: 5px solid #fff;
    img {
      display: block;
      width: 100%;
      max-width: 264px;
      margin: auto;
    }
  }
  .selected {
    border-color: #2d8cf0;
  }
  .selected .checkbox {
    position: absolute;
    top: 15px;
    right: 15px;
    height: 28px;
    width: 28px;
    text-align: center;
    background: #2d8cf0;
    border: 1px solid #1c2438;
  }
}
.ivu-upload {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 120px;
  width: 120px;
  cursor: pointer;
}
.upload-btn {
  display: none;
  height: 120px;
  width: 120px;
  padding-top: 20px;
  text-align: center;
  color: #fff;
  background: rgba(0, 0, 0, .6);
  border-radius: 16%;
}
.ivu-upload:hover .upload-btn {
  display: block;
}

</style>



