<template>
  <div class="sub-content site-msg">
    <h2>消息通知</h2>
    <div class="inner-box">
      <div class="msg-item unread">
        <span class="msg-icon"><Icon type="volume-high"/></span>
        <h3>系统消息</h3>
        <p>这里是消息内容</p>
        <p class="msg-date">2017-12-28 0:11</p>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageSiteMsg',
  props: {
    userInfo: Object
  },
  data () {
    return {
      page: 1,
      total: 0
    }
  },
  created () {
  },
  methods: {
    handleSelect (id) {
      this.$router.push(`/${id}`)
      this.activeName = id
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.msg-item {
  position: relative;
  padding: 32px 32px 32px 132px;
  border-bottom: 1px dashed #ddd;
  .msg-icon {
    position: absolute;
    height: 32px;
    width: 32px;
    top: 32px;
    left: 70px;
    background: #5cadff;
    border-radius: 50%;
    text-align: center;
    .ivu-icon {
      font-size: 24px;
      line-height: 32px;
      color: #fff;
    }
  }
  h3 {
    height: 24px;
    font-size: 16px;
    line-height: 24px;
  }
  p {
    line-height: 20px;
  }
  .msg-date {
    font-size: 12px;
  }
}
.unread {
  color: #5cadff;
}
</style>



