<template>
  <div class="content apps">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem>应用列表</BreadcrumbItem>
      </Breadcrumb>
      <Tabs value="apps_list" @on-click="handleChangeTab">
        <TabPane v-for="item in menus" :key="item.id" :label="item.name" :name="item.id"></TabPane>
      </Tabs>
      <div class="clear">
        <Button type="primary" @click="$router.push('/apps_list_create')" icon="plus" size="large">创建列表</Button>
        <div class="search-box" @keyup.enter="handleSearch">
          <Input v-model="listName" placeholder="列表名称" style="width:200px"/><Button type="primary" @click="handleSearch">搜 索</Button>
        </div>
      </div>
      <Card class="min-height">
        <Table stripe :columns="columns" :data="list"></Table>
        <div class="pages">
          <Page
            v-if="total > 10"
            :total="total"
            show-total
            show-elevator
            @on-change="handlePageChange"/>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageAppsList',
  data () {
    return {
      menus: [
        {id: 'apps', name: '我的应用'},
        {id: 'apps_log', name: '下载日志'},
        {id: 'apps_list', name: '应用列表'}
      ],
      listName: '',
      page: 1,
      total: 0,
      columns: [
        {title: 'id', key: 'id', align: 'center'},
        {title: '标题', key: 'title', align: 'center'},
        {title: '链接', key: 'url', align: 'center'},
        {title: '二维码', key: 'qr_code', align: 'center'},
        {title: '备注', key: 'remark', align: 'center'},
        {title: '查看次数', key: 'view_num', align: 'center'},
        {title: '更新时间', key: 'update_dt', width: '160px', align: 'center'},
        {
          title: '操作',
          key: 'handle',
          width: '250px',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary'
                },
                on: {
                  click: () => {
                    this.handleListPreview(params.row.url)
                  }
                }
              }, '预 览'),
              h('Button', {
                props: {
                  type: 'info'
                },
                on: {
                  click: () => {
                    this.handleListEdit(params.row.id)
                  }
                }
              }, '编 辑'),
              h('Button', {
                props: {
                  type: 'warning'
                },
                on: {
                  click: () => {
                    this.handleListDel(params.row.id, params.row.title)
                  }
                }
              }, '删 除')
            ])
          }
        }
      ],
      list: []
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.handleSearch()
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    handleChangeTab (path) {
      this.$router.push(`/${path}`)
    },
    handlePageChange (page) {
      this.page = page
      this.handleSearch()
    },
    handleSearch () {
      this.$ajax.get('/api/list/pages', {
        params: {
          page: this.page,
          keyword: this.listName
        }
      }).then((res) => {
        if (res.data && res.data.code === '200') {
          const data = res.data.data
          this.list = data.lists || []
          this.total = parseInt(data.pages.total, 10)
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handleListPreview (url) {
      //  TODO 需要修改
      //  window.open(`//dafu.im/${url}`)
    },
    handleListEdit (id) {
      this.$router.push(`/apps_list_create?id=${id}`)
    },
    handleListDel (id, title) {
      this.$Modal.confirm({
        content: `你确定要删除列表【${title}】吗？`,
        onOk: () => {
          this.$ajax.post('/api/list/del', {id}).then((res) => {
            if (res.data && res.data.code === '200') {
              this.$Message.success({
                content: '删除成功',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.apps {
  .wrapper {
    overflow: hidden;
    .clear {
      margin-bottom: 20px;
    }
    button {
      margin-right:10px;
    }
    .search-box {
      float: right;
      button {
        margin: 0 0 0 10px;
      }
    }
  }
}
.pages {
  margin-top: 20px;
}
</style>



