        :autoplay-speed="speed"
        v-model="index"
        :loop="true"
        arrow="never"
        :dots="dots"
        radius-dot>
        <CarouselItem
          v-for="item in carousels"
          :key="item.id">
          <div
            class="carousel-item">
            <img :src="item.img" alt="">
            <div class="carousel-inner">
              <Button
                v-if="item.className"
                :type="item.buttons.type"
                :size="item.buttons.size"
                :class="item.className"
