        </div>
        </Col>
        <Col span="12">
        <div class="inner-box">
          <img src="../assets/images/icon_home_4.png" alt="">
          <h3>iOS APP审核加速</h3>
          <p>帮助您的APP在提交 App Store 的24小时内，就得到官方的审核结果，<br/>
            极大的节省等待时间，最快3小时内上架成功</p>
          <Button type="primary" size="large" style="width:160px">查看详情</Button>
        </div>
        </Col>
      </Row>
    </div>
    <div class="service-intro-3">
      <h3>WAP封装APP</h3>
      <p>一键网站转换APP，完美支持iOS和Android系统程序，自动化操作，无后门</p>
      <p><Button type="primary" size="large" style="width: 160px">立即制作</Button></p>
      <p>
        <span class="service-intro-item" :style="`background:url(${imgs[0]}) no-repeat 50% 0/contain`">自主制作</span>
        <span class="service-intro-item" :style="`background:url(${imgs[1]}) no-repeat 50% 0/contain`">原生开发</span>
        <span class="service-intro-item" :style="`background:url(${imgs[2]}) no-repeat 50% 0/contain`">不限时间</span>
        <span class="service-intro-item" :style="`background:url(${imgs[3]}) no-repeat 50% 0/contain`">个性定制</span>
        <span class="service-intro-item" :style="`background:url(${imgs[4]}) no-repeat 50% 0/contain`">无广告</span>
      </p>
