      <h3>合作伙伴与开发者一起使用大福分发平台</h3>
      <ul class="clearfix">
        <li><img src="../assets/images/logo_tuniu.png" alt=""></li>
        <li><img src="../assets/images/logo_vph.png" alt=""></li>
        <li><img src="../assets/images/logo_jd.png" alt=""></li>
        <li><img src="../assets/images/logo_jrtt.png" alt=""></li>
        <li><img src="../assets/images/logo_163.png" alt=""></li>
        <li><img src="../assets/images/logo_qiniu.png" alt=""></li>
        <li><img src="../assets/images/logo_wyyy.png" alt=""></li>
        <li><img src="../assets/images/logo_bdtj.png" alt=""></li>
        <li><img src="../assets/images/logo_iqiyi.png" alt=""></li>
        <li><img src="../assets/images/logo_360.png" alt=""></li>
        <li><img src="../assets/images/logo_qqbrowse.png" alt=""></li>
        <li><img src="../assets/images/logo_momo.png" alt=""></li>
      </ul>
