<script>
import banner0 from '../assets/images/banner_home_0.jpg'
import banner1 from '../assets/images/banner_home_1.jpg'
import banner2 from '../assets/images/banner_home_2.jpg'
import banner3 from '../assets/images/banner_home_3.jpg'
import bg1 from '../assets/images/icon_home_5.png'
import bg2 from '../assets/images/icon_home_6.png'
import bg3 from '../assets/images/icon_home_7.png'
import bg4 from '../assets/images/icon_home_8.png'
import bg5 from '../assets/images/icon_home_9.png'

export default {
  name: 'PageHome',
  data () {
    return {
      index: 0,
      dots: 'none',
      autoPlay: true,
      speed: 2500,
      imgs: [bg1, bg2, bg3, bg4, bg5]
    }
  },
  created () {
    this.carousels = [
      {img: banner0, id: 0, className: 'banner-btn', buttons: {text: '上传应用', type: 'primary', size: 'large', path: 'release'}},
      {img: banner1, id: 1, className: 'banner-btn banner-btn-1', buttons: {text: '咨询客服', type: 'ghost', size: 'large', path: 'http://wpa.qq.com/msgrd?v=3&uin=3261682395&site=qq&menu=yes'}},
      {img: banner2, id: 2, className: 'banner-btn banner-btn-2', buttons: {text: '立即封装', type: 'ghost', size: 'large', path: 'pack'}},
      {img: banner3, id: 3, className: 'banner-btn banner-btn-3', buttons: {text: '咨询客服', type: 'ghost', size: 'large', path: 'http://wpa.qq.com/msgrd?v=3&uin=1455653215&site=qq&menu=yes'}}
    ]
    this.autoPlay = this.carousels.length >= 4
    this.dots = this.carousels.length >= 4 ? 'inside' : 'none'
  },
  methods: {
    handleClick (path) {
      if (~path.indexOf('//')) {
        window.open(path)
      } else {
        this.$router.push(`/${path}`)
      }
    }
  }
}
</script>

