  > img {
    display: block;
    width: 100%;
    margin: auto;
  }
  > ul {
    position: absolute;
    left: 50%;
    top: 11%;
    width: 50%;
  }
  li {
    position: relative;
    margin-bottom: 6%;
    padding-left: 120px;
    img {
      position: absolute;
      top: 0;
      left: 20px;
    }
    h3 {
      margin-bottom: 15px;
      font-size: 24px;
      line-height: 25px;
      font-weight: normal;
    }
  }
}
.service-intro-2 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
