  }
  h3 {
    margin-bottom: 15px;
    font-size: 24px;
    line-height: 25px;
    font-weight: normal;
  }
  p {
    margin-bottom: 30px;
    font-size: 16px;
    line-height: 30px;
  }
}
.service-intro-3 {
  padding: 45px 0 25px;
  text-align: center;
