    max-width: 996px;
    margin: auto;
    overflow: hidden;
  }
  li {
    float: left;
    margin-top: -1px;
    margin-left: -1px;
    border-left: 1px solid #eee;
    border-top: 1px solid #eee;
    img {
      display: block;
      width: 100%;
    }
  }
}
.banner-btn {
  position: absolute;
  z-index: 1;
  left: 50%;
  bottom: 25%;
  width: 160px;
  margin-left: -80px;
}
.banner-btn-1, .banner-btn-3 {
  left: 18.8%;
  margin-left: 0;
  background: #fff;
}
.banner-btn-2 {
  left: 54.7%;
  margin-left: 0;
  background: #fff;
}
.banner-btn-3 {
}
</style>



