<template>
  <div class="content extract">
    <div class="wrapper">
      <Card :padding="45" shadow>
        <h2 slot="title">IPA下载地址提取</h2>
        <div class="form-box" @keyup.enter="handleSubmit('formExtract')">
          <Form
            ref="formExtract"
            :model="formExtract"
            :rules="ruleExtract">
            <FormItem prop="url">
              <Input
                type="text"
                v-model="formExtract.url"
                placeholder="请输入要提取的URL" />
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                size="large"
                shape="circle"
                icon="android-open"
                long
                @click="handleSubmit('formExtract')">提 取
              </Button>
            </FormItem>
          </Form>
          <table v-if="showInfos">
            <tbody>
            <tr>
              <td>名称</td>
              <td>{{ipaInfos.title}}</td>
            </tr>
            <tr>
              <td>图标</td>
              <td><img :src="ipaInfos.icon" alt=""></td>
            </tr>
            <tr>
              <td>identifier</td>
              <td>{{ipaInfos.bundle_identifier}}</td>
            </tr>
            <tr>
              <td>version</td>
              <td>{{ipaInfos.bundle_version}}</td>
            </tr>
            <tr>
              <td>下载地址</td>
              <td><a :href="ipaInfos.package_url" target="_blank">点击下载</a></td>
            </tr>
            </tbody>
          </table>
        </div>
        <Alert show-icon>目前支持平台 dafuvip, pgyer, i8cv, dafuai, pre, fir。例如：http://dafu.im/a2UvMn</Alert>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageExtract',
  data () {
    return {
      showInfos: false,
      ipaInfos: {
        package_url: '',
        icon: '',
        bundle_identifier: '',
        bundle_version: '',
        title: ''
      },
      formExtract: {
        url: ''
      },
      ruleExtract: {
        url: [
          {required: true, message: '下载地址不能为空', trigger: 'click'}
        ]
      }
    }
  },
  created () {
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$ajax.post('/api/extract/doExtract', this.formExtract).then((res) => {
            if (res.data && res.data.code === '200') {
              this.ipaInfos = res.data.data
              this.showInfos = true
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.extract {
  h2 {
    text-align: center;
    font-weight: normal;
    font-size: 18px;
  }
  .form-box {
    width: 380px;
    margin: auto;
  }
  table {
    width: 100%;
    margin-bottom: 20px;
    border-collapse: collapse;
    border: 1px solid #eee;
    td {
      min-height: 40px;
      padding: 10px 18px;
      border: 1px solid #eee;
      img {
        display: block;
        height: 80px;
        width: 80px;
        border-radius: 16%;
      }
    }
    tr td:first-child {
      width: 150px;
      text-align: right;
    }
    tr:nth-child(odd) td {
      background: #f8f8f9;
    }
  }
  .ivu-alert {
    width: 680px;
    margin: auto;
    font-size: 14px;
  }
}
</style>



