<template>
  <div class="sub-content coupon">
    <h2>优惠券</h2>
    <div class="inner-box">
      <div class="coupon-box">
        <Alert>账户下目前有 {{count}} 张优惠券可用，可以抵扣 ￥<span style="color:#ed3f14">{{value}}</span> 元</Alert>
        <Table stripe :columns="columns" :data="list"></Table>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
      <div class="coupon-qa">
        <h3>优惠券</h3>
        <ul>
          <li>
            <h4>1. 什么是优惠券？</h4>
            <p>优惠券是大福网站发放的一种现金抵扣券，可以直接抵扣现金。</p>
          </li>
          <li>
            <h4>2. 优惠券如何使用？</h4>
            <p>支付时，网站会自动根据账号和优惠券的使用条件，进行自动抵扣。</p>
          </li>
          <li>
            <h4>3. 如何获得更多的优惠券？</h4>
            <p>可以通过邀请好友使用大福网站、参与大福活动等方式获得，请经常关注大福网站，活动多多。</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageCoupon',
  props: {
    userInfo: Object
  },
  data () {
    return {
      page: 1,
      total: 0,
      count: 1,
      value: '5.00',
      columns: [
        {title: '编号', key: '1'},
        {title: '面值', key: '2'},
        {title: '状态', key: '3'},
        {title: '有效期', key: '4'},
        {title: '说明', key: '5'},
        {title: '来源', key: '6'}
      ],
      list: []
    }
  },
  created () {
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.coupon-box {
  margin: 20px 0;
}
.coupon-qa {
  margin-top: 20px;
  h3 {
    height: 40px;
    margin-bottom: 10px;
    border-bottom: 1px solid #ddd;
    line-height: 40px;
  }
  ul {
    width: 60%;
    li {
      padding: 10px 0;
      border-bottom: 1px dashed #ddd;
      h4 {
        height: 30px;
        line-height:30px;
      }
      p {
        color: #999;
      }
    }
    li:last-child {
      border-bottom: 0;
    }
  }
}
</style>



