<template>
  <div class="content udid">
    <div class="wrapper">
      <Card :padding="45" shadow>
        <h2 slot="title">UDID</h2>
        <h3>一步快速获取 iOS 设备的 UDID</h3>
        <p>请使用 iOS 设备上的 Safari 浏览器打开访问：<a :href="url">{{url}}</a></p>
        <p>或者扫描下面的二维码，即可快速获取 UDID</p>
        <div class="qr-code">
          <canvas id="qr_canvas"></canvas>
        </div>
        <div class="qa">
          <h4>什么是UDID?</h4>
          <p>UDID，是iOS设备的一个唯一识别码，每台iOS设备都有一个独一无二的编码，这个编码，我们称之为识别码，也叫做UDID（ Unique Device Identifier）。</p>
          <h4>扫描后系统提示输入密码?</h4>
          <p>如果您扫描后系统提示输入密码，请输入锁屏密码。</p>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import qrCode from 'qrcode'

export default {
  name: 'PageUdid',
  data () {
    return {
      url: 'http://dafu.im/udid'
    }
  },
  created () {
  },
  mounted () {
    this.$nextTick(function () {
      this.canvas = document.getElementById('qr_canvas')
      const url = decodeURIComponent(this.url)
      qrCode.toCanvas(this.canvas, url, {errorCorrectionLevel: 'H'}, (error) => {
        if (error) {
          console.log(error)
        } else {
        }
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.udid {
  text-align: center;
  h2 {
    font-weight: normal;
    font-size: 18px;
  }
  h3 {
    margin-bottom: 20px;
    font-weight: normal;
    font-size: 32px;
  }
  .qr-code {
    height: 150px;
    width: 150px;
    margin: 20px auto;
    border: 1px solid #ddd;
  }
  .qa {
    width: 680px;
    margin: auto;
    padding: 20px 30px;
    border-top: 1px solid #ddd;
    text-align: left;
    h4 {
      margin: 10px 0;
      font-size: 16px;
    }
  }
}
</style>



