<template>
  <div class="content retrieve-pwd">
    <div class="wrapper">
      <Card :padding="45" shadow>
        <h2 slot="title">找回密码</h2>
        <div class="form-box">
          <Form
            ref="formRetrievePwd"
            :model="formRetrievePwd"
            :rules="ruleRetrievePwd">
            <FormItem prop="phone">
              <Input
                type="text"
                v-model="formRetrievePwd.phone"
                placeholder="请输入注册手机号">
              <Icon
                type="android-phone-portrait"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem prop="code">
              <Row :gutter="16">
                <Col span="16">
                <Input
                  type="text"
                  v-model="formRetrievePwd.code"
                  placeholder="请输入短信验证码">
                <Icon
                  type="android-phone-portrait"
                  size="16"
                  slot="prepend"/>
                </Input>
                </Col>
                <Col span="8">
                <Button
                  type="primary"
                  :disabled="btnDisabled"
                  long
                  @click="handleClick">{{btnText}}</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem prop="password">
              <Input
                type="password"
                v-model="formRetrievePwd.password"
                placeholder="请输入新密码，不少于6个字符">
              <Icon
                type="android-unlock"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem prop="confirmPassword">
              <Input
                type="password"
                v-model="formRetrievePwd.confirmPassword"
                placeholder="请再次输入新密码">
              <Icon
                type="android-unlock"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                size="large"
                shape="circle"
                icon="ios-compose-outline"
                long
                @click="handleSubmit('formRetrievePwd')">修 改</Button>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageRetrievePwd',
  data () {
    return {
      btnDisabled: false,
      btnText: '获取验证码',
      formRetrievePwd: {
        phone: '',
        code: '',
        password: '',
        confirmPassword: ''
      },
      ruleRetrievePwd: {
        phone: [
          {required: true, message: '手机号不能为空', trigger: 'click'},
          {message: '手机号格式不正确', pattern: /^1\d{10}$/, trigger: 'click'}
        ],
        code: [
          {required: true, message: '验证码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '验证码位数不正确', trigger: 'click'}
        ],
        password: [
          {required: true, message: '密码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '密码不能少于6个字符', trigger: 'click'}
        ],
        confirmPassword: [
          {validator: this.validatePassCheck, trigger: 'click'}
        ]
      }
    }
  },
  created () {
    //  防止用户刷新页面来清除验证码发送倒计时
    const timingRetrievePwd = window.sessionStorage.getItem('timingRetrievePwd')
    if (timingRetrievePwd) {
      this.btnDisabled = true
      this.cooldown(parseInt(timingRetrievePwd, 10))
    }
  },
  methods: {
    validatePassCheck (rule, value, callback) {
      if (value === '') {
        callback(new Error('确认密码不能为空'))
      } else if (value !== this.formRetrievePwd.password) {
        callback(new Error('密码两次输入不一致'))
      } else {
        callback()
      }
    },
    handleClick () {
      if (!this.formRetrievePwd.phone) return false
      this.$ajax.post('/api/user/genVerifyCode?from=forgot', {phone: this.formRetrievePwd.phone}).then((res) => {
        if (res.data && res.data.code === '200') {
          this.$Message.success('验证码发送成功，请注意查收')
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
      this.btnDisabled = true
      this.cooldown(60)
    },
    cooldown (timing) {
      if (!timing) {
        window.sessionStorage.removeItem('timingRetrievePwd')
        this.btnDisabled = false
        this.btnText = '重新获取'
      } else {
        //  更新 sessionStorage 缓存的计时
        window.sessionStorage.setItem('timingRetrievePwd', timing)
        this.btnText = `${timing}s 后重新获取`
        setTimeout(() => {
          this.cooldown(--timing)
        }, 1000)
      }
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const formRetrievePwd = {
            phone: this.formRetrievePwd.phone,
            code: this.formRetrievePwd.code,
            password: this.formRetrievePwd.password
          }
          this.$ajax.post('/api/user/forgotPassword', formRetrievePwd).then((res) => {
            if (res.data && res.data.code === '200') {
              window.sessionStorage.removeItem('timingRetrievePwd')
              this.$Message.success('密码修改成功')
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.retrieve-pwd {
  h2 {
    text-align: center;
    font-weight: normal;
    font-size: 18px;
  }
  .form-box {
    width: 380px;
    margin: auto;
  }
}
</style>



