<template>
  <div class="content apps">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem>下载日志</BreadcrumbItem>
      </Breadcrumb>
      <Tabs value="apps_log" @on-click="handleChangeTab">
        <TabPane v-for="item in menus" :key="item.id" :label="item.name" :name="item.id"></TabPane>
      </Tabs>
      <div class="clear">
        <div class="search-box" @keyup.enter="handleSearch">
          <DatePicker
            type="date"
            placeholder="选择日期"
            style="width: 200px"
            @on-change="handleDateChange"/><Button type="primary" @click="handleSearch">搜 索</Button>
        </div>
      </div>
      <Card class="min-height">
        <Table stripe :columns="columns" :data="list"></Table>
      </Card>
    </div>
  </div>
</template>

<script>
import formatDate from '../utils/FormatDate'

export default {
  name: 'PageAppsLog',
  data () {
    return {
      menus: [
        {id: 'apps', name: '我的应用'},
        {id: 'apps_log', name: '下载日志'},
        {id: 'apps_list', name: '应用列表'}
      ],
      date: '',
      columns: [
        {title: '名称', key: 'app_name', align: 'center'},
        {title: '下载次数', key: 'total', align: 'center'},
        {title: '下载日期', key: 'days', align: 'center'},
        {
          title: '明细',
          key: 'detail',
          align: 'center',
          render: (h, params) => {
            return h('Button', {
              props: {
                type: 'primary'
              },
              on: {
                click: () => {
                  this.handleWatchDetail(params.row.app_id, params.row.days)
                }
              }
            }, '明 细')
          }
        }
      ],
      list: []
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.date = formatDate(new Date().getTime())
      this.handleSearch()
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    handleChangeTab (path) {
      this.$router.push(`/${path}`)
    },
    handleDateChange (date) {
      this.date = date
    },
    handleSearch () {
      this.$ajax.get('/api/apps/logs', {
        params: {
          date: this.date
        }
      }).then((res) => {
        if (res.data && res.data.code === '200') {
          this.list = res.data.data.logs || []
        } else {
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handleWatchDetail (id, date) {
      this.$router.push(`/apps_log_detail/${id}/${date}`)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.apps {
  .wrapper {
    overflow: hidden;
    .clear {
      margin-bottom: 20px;
    }
    button {
      margin-right:10px;
    }
    .search-box {
      float: right;
      button {
        margin: 0 0 0 10px;
      }
    }
  }
}
</style>



