<template>
  <div class="content app-list-create">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem to="/apps_list">应用列表</BreadcrumbItem>
        <BreadcrumbItem>{{activeText}}</BreadcrumbItem>
      </Breadcrumb>
      <Card :padding="45" shadow>
        <div class="inner-box">
          <Form
            ref="formCreate"
            :model="formCreate"
            :rules="ruleCreate"
            :label-width="120">
            <FormItem label="顶部图片">
              <Upload
                ref="upload"
                :action="`//dafu.im/api/list/background`"
                :with-credentials="true"
                accept=".jpg,.jpeg,.png"
                :format="['jpg', 'jpeg', 'png']"
                :before-upload="handleBeforeUpload"
                :on-success="handleUploadSuccess"
                :on-error="handleUploadError">
                <div class="background">
                  <template v-if="background">
                    <img ref="img" :src="background" alt="">
                  </template>
                  <div :class="['upload-btn', background ? 'edit' : '']">
                    <Icon type="camera" size="40"></Icon>
                    <p>点击{{background ? '更改' : '上传'}}背景</p>
                  </div>
                </div>
              </Upload>
            </FormItem>
            <FormItem prop="title" label="列表名称">
              <Input
                type="text"
                v-model="formCreate.title"
                placeholder="请输入列表名称"
                style="width:400px;margin-right:10px" />
              <span>支持修改APP名称</span>
            </FormItem>
            <FormItem prop="desc" label="列表介绍">
              <Input
                type="text"
                v-model="formCreate.desc"
                placeholder="请输入列表介绍"
                style="width:400px;margin-right:10px" />
            </FormItem>
            <FormItem label="备注">
              <Input
                type="text"
                v-model="formCreate.remark"
                placeholder="请输入备注"
                style="width:400px;margin-right:10px" />
              <span>用于APP区分说明，不显示</span>
            </FormItem>
            <FormItem prop="url" label="短链接">
              <Input
                type="text"
                v-model="formCreate.url"
                placeholder="请输入6位字母数字"
                style="width:400px;margin-right:10px">
              <span slot="prepend">https://dafu.im/</span></Input>
              <span style="position:absolute;left:410px;top:0">支持自定义修改，6位字母数字组合</span>
            </FormItem>
            <FormItem label="添加APP">
              <Input
                type="textarea"
                v-model="formCreate.apps"
                placeholder="如：http://dafu.im/a2UvMn"
                style="width:400px;margin-right:10px" />
              <span>请输入URL地址，每行一个，最多10个</span>
            </FormItem>
            <FormItem>
              <Button type="primary" shape="circle" style="width:120px;margin-right:10px" @click="handleSubmit('formCreate')">保 存</Button>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageAppsListCreate',
  data () {
    return {
      background: '',
      activeText: '创建列表',
      uploadList: [],
      formCreate: {
        title: '',
        desc: '',
        url: '',
        apps: '',
        remark: ''
      },
      ruleCreate: {
        title: [
          {required: true, message: '列表名称不能为空', trigger: 'click'}
        ],
        desc: [
          {required: true, message: '列表介绍不能为空', trigger: 'click'}
        ],
        url: [
          {required: true, message: '短链接不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '短链接不能少于6个字符', trigger: 'click'}
        ],
        apps: [
          {required: true, message: 'App URL不能为空', trigger: 'click'}
        ]
      }
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      //  存在id则为更新列表，否则为新建
      const id = this.$route.query.id
      if (id) {
        this.activeText = '编辑列表'
        this.formCreate.id = id
        //  存在id则获取该列表的详情
        this.$ajax.get('/api/list/detail', {params: {id}}).then((res) => {
          if (res.data && res.data.code === '200') {
            const data = res.data.data
            this.background = data.background
            Object.keys(this.formCreate).forEach((item) => {
              this.formCreate[item] = data[item]
            })
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
      } else {
        //  获取唯一url
        this.$ajax.get('/api/list/getUrl').then((res) => {
          if (res.data && res.data.code === '200') {
            this.formCreate.url = res.data.data
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  mounted () {
    this.uploadList = this.$refs.upload.fileList
  },
  methods: {
    //  限制上传文件数目
    handleBeforeUpload () {
      const check = this.uploadList.length < 1
      if (!check) {
        this.$Message.warning('同时只能上传 1 张图片')
      }
      return check
    },
    handleUploadSuccess (res) {
      this.background = res.data.url
      this.$refs.upload.fileList = []
      this.uploadList = []
    },
    handleUploadError (err) {
      this.$Message.error(err)
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const successMsg = this.formCreate.id ? '列表修改成功' : '列表创建成功'
          this.$ajax.post('/api/list/edit', this.formCreate).then((res) => {
            if (res.data && res.data.code === '200') {
              const router = this.$router
              this.$Message.success({
                content: successMsg,
                duration: 1,
                onClose () {
                  router.push('/apps_list')
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.app-list-create {
  .background {
    position: relative;
    height: 250px;
    width: 576px;
    border: 1px dashed #e1f0fe;
    img {
      display: block;
      height: 100%;
      width: 100%;
    }
    .upload-btn {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -48px 0 0 -39px;
      font-size: 16px;
      text-align: center;
    }
    .edit {
      color: #fff;
    }
  }
}
</style>



