<template>
  <div class="sub-content account">
    <h2>账号信息</h2>
    <div class="inner-box">
      <Form :model="formAccount" :label-width="90">
        <FormItem label="账　　号：">{{userInfo.nickname}}</FormItem>
        <FormItem label="手　　机：">{{userInfo.mobile}}</FormItem>
        <FormItem label="消息通知：">
          <i-switch v-model="formAccount.sms" size="large">
            <span slot="open">开启</span>
            <span slot="close">关闭</span>
          </i-switch>
          <p style="color: #ed3f14">短信将提示：流量购买使用情况、签名到期、上架进度、网站消息等情况。</p>
        </FormItem>
        <FormItem>
          <Button type="primary">保 存</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageAccount',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formAccount: {}
    }
  },
  created () {
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



