<template>
  <div class="pack-rect">
    <div class="pack-purchase">
      <div class="purchase-title">
        <span class="title-line">——</span>
        <span class="title-text">&nbsp;&nbsp;&nbsp;{{boughtItem ? '当前套餐' : '选择套餐'}}&nbsp;&nbsp;&nbsp;</span>
        <span class="title-line">——</span>
      </div>
      <div class="purchase-content">
        <div class="purchase-menu">
          <ul>
            <li v-for="(purchaseItem, itemIndex) in purchaseItemList">
              <div
                class="phone-rect"
                v-bind:class="{selected: (itemIndex === purchaseItemIndex && !boughtItem) || purchaseItem.id === boughtItem}"
                @click="handlePurchaseItemClick(itemIndex)"
                :style="`background-image:url(${purchaseItem.backgroundImage}); cursor:${boughtItem ? 'default': 'pointer'}`"
              >
                <div class="purchase-footmark">
                  <img src="../assets/images/purchase_footmark.png" />
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div v-if="!boughtItem" class="purchase-choices">
          <div style="display: inline-block;">制作次数</div>
          <div style="display: inline-block;">
            <RadioGroup v-model="packAmount">
              <Radio :label="itemGood.id" :key="itemGood.id" v-for="(itemGood, key) in purchaseItemPrices">{{renderPrice(itemGood)}}</Radio>
            </RadioGroup>
          </div>
        </div>
        <div v-if="boughtItem" class="rest-frequency">
          <span>剩余次数</span>
          <span>{{Math.floor(restFrequency)}}</span>
        </div>
        <div v-if="!boughtItem" class="purchase-pay-way">
          <span>支付方式</span>
          <div :class="['pay-item', formSubmit.payment === item.id ? 'active' : '']" v-for="item in payTypes" @click="handleSelectPayway(item.id)">
            <img :src="item.img" :alt="item.name">
            <div class="purchase-footmark">
              <img src="../assets/images/purchase_footmark_small.png" />
            </div>
          </div>
        </div>
        <div v-if="!boughtItem" class="purchase-fee">
          <span>所需费用</span>
          <span class="fee">{{totalPrice === '0.00' ? '免费体验' : ''}}{{totalPrice && totalPrice !== '0.00' ? '￥' + totalPrice : ''}}</span>
        </div>
        <div class="purchase-confirm">
          <Button v-if="!boughtItem" @click="handlePurchaseButton" type="primary">立即购买</Button>
          <!--<Button v-if="boughtItem" @click="handleUpgradePurchase">升级套餐</Button>-->
          <Button v-if="boughtItem" @click="handleGoMyAppButton" type="primary">开始创建APP</Button>
        </div>
      </div>
    </div>
    <div class="pack-purchase-faq">
      <div class="faq-title">常见问题</div>
      <div class="faq-content">
        <ul>
          <li>
            <div class="question">
              <span class="indent-icon">•</span>
              <span class="question-text">如何保证证书不掉不闪，长期稳定不掉线？</span>
            </div>
            <div class="answer">
              <span class="indent-icon"></span>
              <span class="answer-text">我们为您提供上架一条龙服务，1-2年上架成功，专业的iOS技术开发团队，支持任何形式的APP，60000次苹果上架经验，已经成功上架了一火车皮产品~</span>
            </div>
          </li>
          <li>
            <div class="question">
              <span class="indent-icon">•</span>
              <span class="question-text">如何保证证书不掉不闪，长期稳定不掉线？</span>
            </div>
            <div class="answer">
              <span class="indent-icon"></span>
              <span class="answer-text">我们为您提供上架一条龙服务，1-2年上架成功，专业的iOS技术开发团队，支持任何形式的APP，60000次苹果上架经验，已经成功上架了一火车皮产品~</span>
            </div>
          </li>
          <li>
            <div class="question">
              <span class="indent-icon">•</span>
              <span class="question-text">如何保证证书不掉不闪，长期稳定不掉线？</span>
            </div>
            <div class="answer">
              <span class="indent-icon"></span>
              <span class="answer-text">我们为您提供上架一条龙服务，1-2年上架成功，专业的iOS技术开发团队，支持任何形式的APP，60000次苹果上架经验，已经成功上架了一火车皮产品~</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import purchaseItemImage1 from '../assets/images/pack_purchase_1.png'
import purchaseItemImage2 from '../assets/images/pack_purchase_2.png'
import purchaseItemImage3 from '../assets/images/pack_purchase_3.png'
import alipay from '../assets/images/alipay.png'
import weixinpay from '../assets/images/weixinpay.png'
// import unionpay from '../assets/images/unionpay.png'

export default {
  name: 'PagePackPurchase',
  data () {
    return {
      boughtItem: '',
      restFrequency: '1000',
      purchaseItemIndex: -1,
      purchaseItemList: [
        {
          title: '基础版',
          id: '1',
          level: '1',
          backgroundImage: purchaseItemImage1
        }, {
          title: '中级版',
          id: '2',
          level: '2',
          backgroundImage: purchaseItemImage2
        }, {
          title: '高级版(敬请期待)',
          id: '3',
          level: '3',
          backgroundImage: purchaseItemImage3,
          disable: true
        }
      ],
      purchaseItemPrices: [],
      payTypes: [
        {name: '支付宝', id: 'alipay', img: alipay},
        {name: '微信支付', id: 'weixin', img: weixinpay}
//        {name: '银联支付', id: 'union', img: unionpay}
      ],
      packAmount: '',
      totalPrice: '',
      formSubmit: {
        type: '100000',
        goods_id: this.$route.query.goods_id || '0',
        payment: this.$route.query.payment || ''
      }
    }
  },
  created () {
    if (this.$route.query.itemindex) {
      console.log('this.route.query', this.$route.query)
    }
    this.$ajax.get('user/my-services?type=1', {type: 1}).then((response) => {
      console.log('user/my-services', response)
      const serviceLevel = (response.data.data || {}).level
      let matchServiceIndex = 1
      let matchServiceId = ''
      this.purchaseItemList.map((serviceInfo, index) => {
        if (serviceInfo.level === serviceLevel) {
          matchServiceIndex = index
          matchServiceId = serviceInfo.id
        }
      })
      this.purchaseItemIndex = matchServiceIndex
      this.restFrequency = (response.data.data || {}).limited_nums
      this.boughtItem = matchServiceId
      if (Number(this.restFrequency) === 0) {
        this.boughtItem = ''
      }
      if (this.$route.query.mode === 'purchase') {
        this.boughtItem = ''
      }
      // 如果有有效套餐则跳转到配置页
      if (this.boughtItem) {
        this.$router.push('/pack_setting')
      }
    })
  },
  watch: {
    purchaseItemIndex (cVal) {
      this.totalPrice = '正在计算 ...'
      this.packAmount = ''
      if (this.purchaseItemList[cVal].disable) {
        this.$Message.info('当前版本暂未开放，敬请期待！')
        this.purchaseItemIndex = this.purchaseItemIndex - 1
        return
      }
      this.$ajax.get(`/service/lists?type=1&serviceId=${this.purchaseItemList[cVal].id}`).then((response) => {
        console.log('/service/lists', response)
        this.purchaseItemPrices = response.data.data.goods
        this.packAmount = this.purchaseItemPrices[this.purchaseItemPrices.length - 1].id
      })
    },
    packAmount (cVal) {
      this.formSubmit.goods_id = cVal
      this.totalPrice = (this.purchaseItemPrices.filter((price) => { return price.id === cVal })[0] || {}).price
    }
  },
  methods: {
    handlePurchaseItemClick (itemIndex) {
      if (this.boughtItem) return
      this.purchaseItemIndex = itemIndex
      this.formSubmit.goods_id = this.purchaseItemList[itemIndex].id
    },
    handleSelectPayway (paywayId) {
      this.formSubmit.payment = paywayId
    },
    handlePurchaseButton () {
      // 如果用户已经登录
      if (window.localStorage.getItem('isLogin')) {
        if (this.purchaseItemIndex === -1) {
          this.$Message.info('请选择套餐')
          return
        }
        if (this.formSubmit.goods_amount === 0) {
          this.$Message.info('请选择制作次数')
          return
        }
        if (this.formSubmit.payment === '') {
          this.$Message.info('请选择支付方式')
          return
        }
        //  异步请求前创建新窗口，不然会被 chrome 拦截
        const newWindow = window.open('about:blank')
        //  创建订单
        this.$ajax.post('/pay/center', this.formSubmit).then((res) => {
          if (res.data && res.data.code === '200') {
            const data = res.data.data
            let url = 'about:blank'
            // 如果是免费体验套餐
            if (String(data.is_payed) === '1') {
              const handleOk = this.handleOk()
              newWindow.close()
              this.$Message.success({
                content: '购买成功',
                duration: 3,
                onClose () {
                  handleOk()
                }
              })
            } else {
              // 如果是微信支付
              if (this.formSubmit.payment === 'weixin') {
                //  需要和当前路由模式匹配，当前为 hash 模式
                url = `/pay_weixin?qr_code=${encodeURIComponent(data.url)}&order_sn=${data.trade_id}`
              } else {
                url = this.$ajax.defaults.baseURL + data.url
              }
              console.log(newWindow)
              newWindow.location = url
              this.$Modal.confirm({
                content: '<h3 style="margin-bottom:10px">请在新开窗口完成支付</h3><p style="color:#999">并根据您的支付情况完成以下操作</p>',
                okText: '完成支付',
                cancelText: '支付遇到问题？',
                onOk: this.handleOk,
                onCancel: this.handleCancel
              })
            }
          } else {
            newWindow.close()
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
      } else {
        const router = this.$router
        const { formSubmit, purchaseItemIndex } = this
        this.$Message.info({
          content: '请登录后继续下一步操作，即将跳转到登录页面...',
          duration: 3,
          onClose () {
            const path = `/login#pack_purchase?goods_id=${formSubmit.goods_id}&payment=${formSubmit.payment}&itemindex=${purchaseItemIndex}`
            router.push(path)
          }
        })
      }
    },
    handleUpgradePurchase () {
      this.boughtItem = ''
    },
    handleGoMyAppButton () {
      this.$router.push(`/pack_setting`)
    },
    renderPrice (priceData) {
      const limitUnitList = {
        day: '天',
        week: '星期',
        month: '月',
        year: '年'
      }
      let priceString = ''
//      priceString = priceString + priceData.limited_nums + '次'
//      if (priceData.limited_nums > 0) {
//        if (priceData.limited_nums < 0) {
//          priceString = `${priceData.limited_nums}${limitUnitList[priceData.limited_unit]}/无限次`
//        } else {
//          priceString = priceString + `(${priceData.limited_nums}${limitUnitList[priceData.limited_unit]}有效)`
//        }
//      }
      priceString = priceString + priceData.limited_nums + '个'
      if (parseInt(priceData.limited_time || '-1') > -1) {
        priceString = priceString + ` (${priceData.limited_time}${limitUnitList[priceData.limited_unit]}有效)`
      } else {
        priceString = priceString + ' (永久)'
      }
      return priceString
    },
    handleOk () {
      window.location.href = location.pathname
    },
    handleCancel () {
      console.log('purchase cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
  .pack-purchase {
    background-color: #fff;
    width: 90%;
    min-width: 1200px;
    margin: 20px auto;
    text-align: center;
    .purchase-title {
      width: 100%;
      margin-top: 30px;
      color: #333;
      font-size: 24px;
      display: inline-block;
      .title-text {
        letter-spacing: 2px;
      }
      .title-line {
        color: #ccc;
      }
    }
    .purchase-content {
      width: 1200px;
      min-width: 1200px;
      margin: 20px auto 0 auto;
      text-align: left;
      display: inline-block;
      padding: 10px 84px;
      .purchase-menu {
        display: inline-block;
        margin-bottom: 20px;
        ul {
          li {
            float: left;
            margin: 10px 15px;
            .phone-rect {
              width: 312px;
              height: 520px;
              background-repeat: no-repeat;
              background-position: center;
              background-size: 100% auto;
              border-radius: 6px;
              border: #e3e3e3 1px solid;
              position: relative;
              transition: all 250ms;
              .purchase-footmark {
                width: 30px;
                height: 30px;
                bottom: 0;
                right: 0;
                position: absolute;
                display: none;
                img {
                  width: 100%!important;
                  height: 100%!important;
                  margin: 0!important;
                }
              }
              &:hover {
                margin-top: -10px;
                box-shadow: 0 2px 9px rgba(0, 0, 0, 0.175);
                border-color: #dadada;
              }
              &.selected {
                /*box-shadow: 0 0 5px rgba(5, 124, 210, 1);*/
                border-color: #348dee;
                .purchase-footmark {
                  display: inline-block;
                }
              }
            }
          }
        }
      }
    }
    img {
      margin-top: 30px;
      max-width: 200px;
    }
    .purchase-confirm {
      padding: 20px 0 50px 0;
      text-align: center;
      Button {
        min-width: 125px;
        margin: 0 10px;
      }
    }
    .purchase-choices,.purchase-fee,.rest-frequency,.purchase-pay-way {
      line-height: 36px;
      margin: 10px 0;
      > :nth-child(1) {
        min-width: 80px;
        margin-left: 15px;
        line-height: 38px;
        font-weight: 400;
        display: inline-block;
        color: #9ea7b4;
      }
      .fee {
        font-size: 16px;
        font-weight: bold;
        color: #f00;
      }
      .ivu-radio-group label {
        margin-right: 40px;
      }
      .ivu-radio-wrapper {
        vertical-align: initial;
        font-size: 14px;
      }
      .purchase-footmark {
        width: 16px;
        height: 16px;
        bottom: 0;
        right: 0;
        position: absolute;
        display: none;
        img {
          width: 100%!important;
          height: 100%!important;
          margin: 0!important;
        }
      }
    }
    .pay-item {
      height: 40px;
      line-height: 40px;
      display: inline-block;
      border: #dadada 1px solid;
      position: relative;
      transition: border-color 250ms;
      img {
        width: auto;
        height: 100%;
        margin: 0 auto;
      }
      &.active .purchase-footmark {
        display: inline-block;
        line-height: 0;
      }
    }
    .purchase-pay-way {
      display: inline-block;
      cursor: pointer;
      > * {
        float: left;
      }
      > div {
        margin-right: 20px;
      }
      > .active {
        border-color: #348dee;
      }
    }
  }
  .pack-purchase-faq {
    width: 90%;
    min-width: 1200px;
    margin: 40px auto 0 auto;
    min-height: 200px;
    .faq-title {
      font-size: 16px;
      font-weight: bold;
      line-height: 30px;
    }
    .faq-content {
      ul {
        li {
          border-left: transparent 3px solid;
          transition: border,background 300ms ease-in;
          line-height: 24px;
          margin: 10px 0;
          * {
            cursor: pointer;
          }
          .indent-icon {
            width: 30px;
            margin-right: 10px;
            display: inline-block;
            text-align: right;
          }
          .answer {
            display: none;
          }
        }
        li:hover {
          background-color: #fff;
          border-color: #555;
          padding: 12px 0;
          .indent-icon {
            font-weight: bold;
          }
          .question-text {
            font-size: 16px;
            font-weight: bold;
          }
          .answer {
            display: block;
          }
        }
      }
    }
  }
</style>



