<template>
  <div class="sub-content progress">
    <h2>上架进度</h2>
    <div class="inner-box">
      <div class="progress-box">
        <Table stripe :columns="columns" :data="list"></Table>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageProgress',
  props: {
    userInfo: Object
  },
  data () {
    return {
      page: 1,
      total: 0,
      columns: [
        {title: '时间', key: '1'},
        {title: 'APP名称', key: '2'},
        {title: '类型', key: '3'},
        {title: '上架内容', key: '4'},
        {title: '上架进度', key: '5'},
        {title: '状态', key: '6'}
      ],
      list: []
    }
  },
  created () {
  },
  methods: {
    handlePageChange (page) {
      this.page = page
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.search-box {
  span {
    margin: 0 5px;
  }
  button {
    margin-left: 10px;
  }
}
.progress-box {
  margin: 20px 0;
}
</style>



