<template>
  <div class="sign">
    <div class="banner">
      <img src="../assets/images/banner_sign.jpg" alt="">
      <Button type="primary" size="large" style="width: 160px" @click="handleOpenWpa">咨询客服</Button>
    </div>
    <div class="service-intro-1">
      <div class="inner-box">
        <Row>
          <Col span="8">
          <img src="../assets/images/icon_home_1.png" alt="">
          <h4>TestFlight签名</h4>
          <p>无需信任证书，100%不闪退</p>
          <p>快速分发应用游戏</p>
          <p>全自动解决分发内测问题</p>
          </Col>
          <Col span="8">
          <img src="../assets/images/icon_home_0.png" alt="">
          <h4>苹果企业证书</h4>
          <p>多张证书，分类签发，长久稳定</p>
          <p>无需越狱，无限安装</p>
          <p>支持包月/季度/半年/全年</p>
          </Col>
          <Col span="8">
          <img src="../assets/images/icon_home_2.png" alt="">
          <h4>个人签—个性定制</h4>
          <p>UDID个性定制，一年内不闪退</p>
          <p>无需证书信任</p>
          <p>支持批量和单个定制</p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-2">
      <h3>三种签名的差别</h3>
      <div class="inner-box">
        <Table border :columns="tableColumns" :data="tableData"/>
      </div>
    </div>
    <div class="service-intro-3">
      <img src="../assets/images/bg_sign_0.jpg" alt="">
    </div>
    <div class="service-intro-4">
      <div class="inner-box">
        <Row>
          <Col span="6">
          <img src="../assets/images/icon_sign_0.png" alt="">
          <h4>专业的开发团队</h4>
          <p>平均6年的iOS开发经验</p>
          <p>提供技术咨询服务，排查解答技术疑问等</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_sign_1.png" alt="">
          <h4>多种签名方式&套餐选择</h4>
          <p>根据用户不同的需求，提供三种签名方式</p>
          <p>以及对应的不同套餐，为用户提供最经济实惠的服务</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_sign_2.png" alt="">
          <h4>提供合同&发票</h4>
          <p>为保证用户权益</p>
          <p>我们支持提供合同以及发票服务</p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_sign_3.png" alt="">
          <h4>7*24小时客服服务</h4>
          <p>我们将提供24小时全天候专业客服服务</p>
          <p>随时随刻为客户提供服务</p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-5" :style="`background:#fff url(${imgs[0]}) no-repeat 25% 50%/31.58% auto`">
      <div class="inner-box">
        <Row>
          <Col span="12" offset="12">
          <h3>签名工具</h3>
          <p>一键签名工具，签名快速稳定，</p>
          <p>同时支持苹果企业证书&UDID个人签证书，</p>
          <p>汉化操作,一步到位</p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-6">
      <img src="../assets/images/bg_sign_2.jpg" alt="">
      <Button type="primary" size="large" style="width: 160px" @click="handleOpenWpa">立即咨询</Button>
    </div>
  </div>
</template>

<script>
import bg0 from '../assets/images/bg_sign_1.png'

export default {
  name: 'PageSign',
  data () {
    return {
      tableColumns: [
        {
          title: ' ',
          key: 'key0'
        },
        {
          title: 'TestFlight',
          key: 'key1'
        },
        {
          title: '苹果企业签名',
          key: 'key2'
        },
        {
          title: 'UDID个人签定制',
          key: 'key3'
        }
      ],
      tableData: [
        {
          key0: '优点',
          key1: '三个月内绝对不闪退',
          key2: '无需越狱，无限安装',
          key3: '一年内不闪退'
        },
        {
          key0: '缺点',
          key1: '三个月后，必须重新下载',
          key2: '需要用户手动点击证书信任',
          key3: '需提供设备的UDID，才能进行绑定操作'
        },
        {
          key0: '适用环境',
          key1: '适用于用户众多，中长期运营的APP',
          key2: '适用于各种类型的APP运营，短期、长期皆可',
          key3: '适用于少数APP测试适用'
        },
        {
          key0: '价格',
          key1: '100元 /100个',
          key2: '500元/1个月',
          key3: '50元/1个'
        }
      ],
      imgs: [bg0]
    }
  },
  methods: {
    handleOpenWpa () {
      window.open('http://wpa.qq.com/msgrd?v=3&uin=3261682395&site=qq&menu=yes')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner {
  position: relative;
  img {
    display: block;
    width: 100%;
    margin: auto;
  }
  button {
    position: absolute;
    top: 65%;
    left: 23%;
    // margin: -18px 0 0 -79px;
  }
}
.service-intro-1 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
  .inner-box {
    width: 70%;
    margin: auto;
    overflow: hidden;
    .ivu-col {
      margin-left: -1px;
      border-left: 1px solid #eee;
    }
    img {
      margin-bottom: 10px;
    }
    h4 {
      margin-bottom: 10px;
      font-weight: normal;
      font-size: 24px;
      color: #333;
    }
    p {
      font-size: 16px;
    }
  }
}
.service-intro-2 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
  h3 {
    margin-bottom: 40px;
    font-size: 36px;
    font-weight: normal;
    color: #333;
  }
  .inner-box {
    width: 70%;
    margin: auto;
    .ivu-table-cell span {
      font-size: 16px;
    }
  }
}
.service-intro-3 {
  background: #2e88fe;
  img {
    display: block;
    width: 100%;
    max-width: 2000px;
    margin: auto;
  }
}
.service-intro-4 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
  .inner-box {
    width: 70%;
    margin: auto;
    img {
      margin-bottom: 10px;
    }
    h4 {
      margin-bottom: 10px;
      font-weight: normal;
      font-size: 18px;
      color: #333;
    }
  }
}
.service-intro-5 {
  .inner-box {
    width: 70%;
    margin: auto;
    padding: 185px 0 160px;
    border-top: 1px solid #eee;
  }
  h3 {
    margin:0 0 20px 160px;
    font-size: 36px;
    font-weight: normal;
  }
  p {
    margin: 0 0 10px 160px;
    font-size: 18px;
  }
}
.service-intro-6 {
  position: relative;
  img {
    display: block;
    width: 100%;
    max-width: 2000px;
    margin: auto;
  }
  button {
    position: absolute;
    top: 70%;
    left: 50%;
    margin-left:-79px;
  }
}
</style>



