<template>
  <div class="header">
    <div class="wrapper">
      <h1 id="logo" class="logo"><img src="../assets/images/logo.png" alt="dafu.im"></h1>
      <div class="menu">
        <template v-if="isLogin">
          <!--<Avatar icon="person" size="small"/>-->
          <!--<Dropdown-->
            <!--placement="bottom-end"-->
            <!--class="user-menu"-->
            <!--@on-click="handleClick">-->
            <!--<a href="javascript:void(0)" class="nickname">欢迎您，{{userInfo.nickname || userInfo.mobile}}<Icon type="arrow-down-b"/></a>-->
            <!--<DropdownMenu slot="list">-->
              <!--<DropdownItem-->
                <!--v-for="item in userMenus"-->
                <!--:key="item.id"-->
                <!--:name="item.id"-->
                <!--:divided="item.divided"><span class="icon"><Icon :type="item.icon"/></span><span>{{item.name}}</span></DropdownItem>-->
            <!--</DropdownMenu>-->
          <!--</Dropdown>-->
          <div class="user-info">
            <span>欢迎您，</span>
            <span class="user-info-menu">
              <span class="nick-name">{{userInfo.nickname || userInfo.mobile}}</span>
              <span class="menu-arrow"></span>
              <div class="menu-drap-down">
                <ul>
                  <li>
                    <a href="/setting/account">账号信息</a>
                  </li>
                  <li>
                    <a href="/user/myorder">我的订单</a>
                  </li>
                  <li>
                    <a href="/user/financial">申请发票</a>
                  </li>
                  <li>
                    <a href="/logout">退出登录</a>
                  </li>
                </ul>
              </div>
            </span>
          </div>
          <!--<div class="badge">-->
            <!--<a v-if="parseInt(userInfo.download_num, 10) <= 0" href="#" style="margin-right:10px">购买次数去广告</a>-->
            <!--<Badge dot>-->
              <!--<a href="/setting/site_msg"><Icon type="ios-bell-outline" size="22"></Icon></a>-->
            <!--</Badge>-->
          <!--</div>-->
          <div class="flow-info">
            <!--<div class="flow-info-item">-->
                <!--<div>今日剩余免费下载次数-->
                  <!--<Poptip trigger="hover" placement="bottom">-->
                    <!--<Icon type="ios-help-outline" :size="18" color="#5cadff"/>-->
                    <!--<div slot="content" style="text-align:left">-->
                      <!--<p>1. 每天免费200次下载，24点更新</p>-->
                      <!--<p>2. 账号下所有App共享剩余下载次数</p>-->
                    <!--</div>-->
                  <!--</Poptip>-->
                <!--</div>-->
                <!--<strong>{{userInfo.free_download_num}}</strong>-->
            <!--</div>-->
            <!--<div class="flow-info-item">-->
                <!--<div>剩余付费下载次数-->
                  <!--<Poptip trigger="hover" placement="bottom">-->
                    <!--<Icon type="ios-help-outline" :size="18" color="#5cadff"/>-->
                    <!--<div slot="content" style="text-align:left">-->
                      <!--<p>1. 优先使用付费次数，再使用免费次数</p>-->
                      <!--<p>2. 账号下所有App共享剩余下载次数</p>-->
                    <!--</div>-->
                  <!--</Poptip>-->
                <!--</div>-->
                <!--<strong>{{userInfo.download_num}}</strong>-->
            <!--</div>-->
            <!--<div class="flow-info-btn">-->
              <!--<Button type="primary">购买次数</Button>-->
            <!--</div>-->
          </div>
        </template>
        <template v-else>
          <Button type="primary" @click="handleClick('login')">登 录</Button><Button type="ghost" @click="handleClick('sign_up')">注 册</Button>
        </template>
      </div>
      <ul class="navigation">
        <li v-for="item in navigation">
          <router-link :class="{'router-link-active': activeListHasThis(item.subPaths)}" :to="`/${item.id}`">{{item.name}}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      navigation: [],
      userMenus: []
    }
  },
  props: {
    isLogin: Boolean,
    userInfo: Object
  },
  created () {
    //  导航菜单配置项，后期应后台获取
    this.navigation = [
      {
        id: 'index',
        name: '首页',
        subPaths: []
      },
      {
        id: 'pack',
        name: '封装',
        subPaths: [
          '/pack_purchase',
          '/pack_setting'
        ]
      },
      {
        id: 'sale',
        name: '上架',
        subPaths: []
      },
      {
        id: 'sign',
        name: '签名',
        subPaths: []
      },
//      {
//        id: 'apps',
//        name: '发布',
//        subPaths: []
//      },
      {
        id: 'kit',
        name: '工具箱',
        subPaths: []
      }
//      {
//        id: 'price',
//        name: '价格',
//        subPaths: []
//      }
    ]
    //  用户菜单配置项，后期应后台获取
    this.userMenus = [
      {name: '基本设置', id: 'setting', icon: 'android-settings'},
      {name: '应用管理', id: 'apps', icon: 'grid'},
      {name: '增值服务', id: 'service', icon: 'android-cart'},
      {name: '退出', id: 'logout', icon: 'android-open', divided: true}
    ]
  },
  methods: {
    handleClick (path) {
      if (path === 'logout') {
        this.handleLogout()
      } else {
        this.$router.push(`/${path}`)
      }
    },
    handleLogout () {
      //  用户登出
      this.$ajax.get('/api/user/logout').then((res) => {
        if (res.data && res.data.code === '200') {
          window.localStorage.setItem('userToken', null)
          window.location.reload()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    activeListHasThis (subPaths) {
      const { path } = this.$router.currentRoute
      return subPaths.filter((subPath, index) => {
        return path === subPath
      }).length
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.header {
  background: #fff;
  box-shadow: 0 1px 1px 0 #eee;
  .wrapper {
    height: 90px;
    .logo {
      float: left;
      margin: 26px 32px 0 0;
      img {
        display: block;
        margin: auto;
      }
    }
  }
  .badge {
    float: right;
    margin: 30px 20px 0 0;
    a {
      color: #666;
    }
    a:hover {
      text-decoration: underline;
    }
  }
}
.user-info {
  margin-top: 32px;
  .user-info-menu {
    display: inline-block;
    position: relative;
    padding-right: 20px;
    cursor: pointer;
    transition: border-color,color 200ms ease-in-out;
    border-bottom: transparent 2px solid;
    &:hover {
      border-bottom: #2d8cf0 2px solid;
      color: #2d8cf0;
      .menu-drap-down {
        display: inline-block;
      }
    }
    .menu-arrow {
      width: 0;
      height: 0;
      right: 0;
      top: 8px;
      display: inline-block;
      border-width: 4px;
      border-style: solid;
      border-color: transparent;
      border-top-color: #999;
      position: absolute;
    }
    .menu-drap-down {
      width: 113px;
      margin-top: 2px;
      margin-left: -1px;
      display: none;
      border-radius: 3px;
      border: #dadada 1px solid;
      box-shadow: 0 0 12px rgba(30, 30, 30, 0.2);
      position: absolute;
      background-color: #fff;
      z-index: 200;
      padding: 3px 0;
      ul li {
        a {
          width: 100%;
          display: inline-block;
          line-height: 30px;
          font-size: 12px;
          text-indent: 30px;
          color: #777;
          cursor: pointer;
        }
        a:hover {
          color: #2d8cf0;
        }
        & + li {
          border-top: #f1f1f1 1px solid;
        }
      }
    }
  }
}
.menu {
  float: right;
  button {
    margin: 29px 10px 0 0;
  }
  .ivu-poptip {
    vertical-align: middle;
  }
}
.user-menu {
  float: right;
  margin-top: 30px;
  .ivu-dropdown-rel {
    a {
      display: inline-block;
      height: 24px;
      vertical-align: middle;
      line-height: 24px;
      color: #666;
      .ivu-icon {
        margin: 0 5px;
      }
    }
  }
  .ivu-dropdown-item {
    line-height: 16px;
    .ivu-icon {
      font-size: 16px;
    }
    span {
      display: inline-block;
      vertical-align: top;
    }
    .icon {
      width: 20px;
    }
  }
}
.flow-info {
  padding-top: 5px;
  clear: both;
  .flow-info-item {
    float: left;
    padding: 0 10px;
    border-right: 1px solid #eee;
    text-align: right;
  }
  .flow-info-btn {
    float: left;
    padding-left: 20px;
    button {
      margin: 5px 0 0 0;
    }
  }
}
.navigation {
  float: left;
  margin-right: 20px;
  li {
    position: relative;
    float: left;
    a {
      display: block;
      height: 90px;
      padding: 0 25px;
      line-height: 90px;
      color: #666;
    }
    a::after {
      content: '';
      position: absolute;
      /*left: 50%;*/
      /*right: 50%;*/
      width: 100%;
      left: 0;
      bottom: 0;
      background: transparent;
      height: 2px;
      -webkit-transition-property: 'background-color';
      transition-property: 'background-color';
      -webkit-transition-duration: .3s;
      transition-duration: .3s;
      -webkit-transition-timing-function: ease-out;
      transition-timing-function: ease-out;
    }
    a:hover::after {
      /*left: 0;*/
      /*right: 0;*/
      background-color: #2d8cf0;
    }
    a:hover, .router-link-active {
      color: #2d8cf0;
    }
    .router-link-active:after {
      /*left: 0;*/
      /*right: 0;*/
      background-color: #2d8cf0;
    }
  }
}
</style>



