<template>
  <div class="kit">
    <div class="banner">
      <img src="../assets/images/banner_kit.jpg" alt="">
      <div class="inner-box">
        <Row>
          <Col span="12">
          <h3>获取UDID</h3>
          <p>用iOS设备扫描获取二维码，或者<br/>
            safari输入dafu.im/udid，即可获取</p>
          <Button type="primary" size="large" style="width: 160px" @click="$router.push('/udid')">立即获取</Button>
          </Col>
          <Col span="12">
          <h3>提取iPA包</h3>
          <p>输入iPA包的下载地址，即可提取iPA包</p>
          <Button type="primary" size="large" style="width: 160px" @click="$router.push('/extract')">立即提取</Button>
          </Col>
        </Row>
      </div>
    </div>
    <div class="qa-list">
      <div class="inner-box">
        <h2><Icon type="android-compass" color="#2d8cf0" />常见问题</h2>
        <Row :gutter="16">
          <Col span="12">
          <Card>
            <h3 slot="title">签名相关</h3>
            <a href="#" slot="extra">更多 &raquo;</a>
            <ul>
              <li v-for="item in questionList">
                <a :href="item.url" target="_blank">&rsaquo; {{item.name}}</a>
              </li>
            </ul>
          </Card>
          </Col>
          <Col span="12">
          <Card>
            <h3 slot="title">上架相关</h3>
            <a href="#" slot="extra">更多 &raquo;</a>
            <ul>
              <li v-for="item in questionList">
                <a :href="item.url" target="_blank">&rsaquo; {{item.name}}</a>
              </li>
            </ul>
          </Card>
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageKit',
  data () {
    return {
      questionList: []
    }
  },
  created () {
    this.questionList = [
      {name: '保证证书不掉不闪，长期稳定不掉线？', url: '#'},
      {name: '向您承诺负责、高效的签名服务有效管理？', url: '#'},
      {name: '让 APP 告别添加 UDID，为您带来可靠性更高的？', url: '#'},
      {name: '同时不限制设备安装数量？', url: '#'}
    ]
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner {
  position: relative;
  > img {
    display: block;
    width: 100%;
    margin: auto;
  }
  .inner-box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 66%;
    margin-left: -33%;
    text-align: center;
  }
  h3 {
    margin-bottom: 3%;
    font-size: 36px;
    font-weight: normal;
    color: #333;
  }
  p {
    height: 48px;
    margin-bottom: 3%;
    font-size: 16px;
  }
}
.qa-list {
  padding: 45px 0;
  background: #fff;
  .inner-box {
    width: 70%;
    margin: auto;
  }
  h2 {
    margin-bottom: 20px;
    font-weight: normal;
    font-size: 30px;
    color: #2d8cf0;
    .ivu-icon {
      margin-right: 5px;
    }
  }
  a {
    color: #999;
  }
  li {
    font-size: 16px;
    line-height: 30px;
  }
}
</style>



