<template>
  <div class="content sign-up">
    <div class="wrapper">
      <Card :padding="45" shadow>
        <h2 slot="title">用户注册</h2>
        <div class="form-box" @keyup.enter="handleSubmit('formSignUp')">
          <Form
            ref="formSignUp"
            :model="formSignUp"
            :rules="ruleSignUp">
            <FormItem prop="mobile">
              <Input
                type="text"
                v-model="formSignUp.mobile"
                placeholder="请输入手机号">
              <Icon
                type="android-phone-portrait"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem prop="code">
              <Row :gutter="16">
                <Col span="16">
                <Input
                  type="text"
                  v-model="formSignUp.code"
                  placeholder="请输入短信验证码">
                <Icon
                  type="android-phone-portrait"
                  size="16"
                  slot="prepend"/>
                </Input>
                </Col>
                <Col span="8">
                <Button
                  type="primary"
                  :disabled="btnDisabled"
                  long
                  @click="handleClick">{{btnText}}</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem prop="password">
              <Input
                type="password"
                v-model="formSignUp.password"
                placeholder="请输入密码，不少于6个字符">
              <Icon
                type="android-unlock"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem prop="confirmPassword">
              <Input
                type="password"
                v-model="formSignUp.confirmPassword"
                placeholder="请再次输入密码">
              <Icon
                type="android-unlock"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                size="large"
                shape="circle"
                icon="android-checkmark-circle"
                long
                @click="handleSubmit('formSignUp')">注 册</Button>
            </FormItem>
            <FormItem class="link"><span>已有账号? </span> <a href="javascript:void(0);" @click="$router.push('/login')">立刻登录</a><a href="javascript:void(0);" class="right" @click="$router.push('/retrieve_pwd')">忘记密码？</a></FormItem>
          </Form>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import { getUserToken, writeUserToken } from '../utils/GetAxiosBaseValue'
export default {
  name: 'PageSignUp',
  data () {
    return {
      btnDisabled: false,
      btnText: '获取验证码',
      formSignUp: {
        mobile: '',
        code: '',
        password: '',
        confirmPassword: ''
      },
      ruleSignUp: {
        mobile: [
          {required: true, message: '手机号不能为空', trigger: 'click'},
          {message: '手机号格式不正确', pattern: /^1\d{10}$/, trigger: 'click'}
        ],
        code: [
          {required: true, message: '验证码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '验证码位数不正确', trigger: 'click'}
        ],
        password: [
          {required: true, message: '密码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '密码不能少于6个字符', trigger: 'click'}
        ],
        confirmPassword: [
          {validator: this.validatePassCheck, trigger: 'click'}
        ]
      }
    }
  },
  created () {
    //  防止用户刷新页面来清除验证码发送倒计时
    const timing = window.sessionStorage.getItem('timing')
    if (timing) {
      this.btnDisabled = true
      this.cooldown(parseInt(timing, 10))
    }
  },
  methods: {
    validatePassCheck (rule, value, callback) {
      if (value === '') {
        callback(new Error('确认密码不能为空'))
      } else if (value !== this.formSignUp.password) {
        callback(new Error('密码两次输入不一致'))
      } else {
        callback()
      }
    },
    handleClick () {
      if (!this.formSignUp.mobile) return false
      this.$ajax.post('/user/send-code', {mobile: this.formSignUp.mobile}).then((res) => {
        if (res.data && res.data.code === '200') {
          this.$Message.success('验证码发送成功，请注意查收')
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
      this.btnDisabled = true
      this.cooldown(60)
    },
    //  获取验证码按钮的冷却函数
    cooldown (timing) {
      if (!timing) {
        window.sessionStorage.removeItem('timing')
        this.btnDisabled = false
        this.btnText = '重新获取'
      } else {
        //  更新 sessionStorage 缓存的计时
        window.sessionStorage.setItem('timing', timing)
        this.btnText = `${timing}s 后重新获取`
        setTimeout(() => {
          this.cooldown(--timing)
        }, 1000)
      }
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const formSignUp = {
            mobile: this.formSignUp.mobile,
            code: this.formSignUp.code,
            password: this.formSignUp.password
          }
          const ajax = this.$ajax
          const router = this.$router
          const Message = this.$Message
          ajax.post('/user/register', formSignUp).then((res) => {
            if (res.data && res.data.code === '200') {
              writeUserToken(res.data.data.token)
              this.$ajax.defaults.headers = getUserToken()
              window.sessionStorage.removeItem('timing')
              Message.info({
                content: '注册成功，即将自动登录...',
                duration: 1,
                onClose () {
                  //  自动登录
//                  ajax.post('/api/user/login', {
//                    phone: formSignUp.phone,
//                    password: formSignUp.password,
//                    autoLogIn: 0
//                  }).then((_res) => {
//                    if (_res.data && _res.data.code === '200') {
//                      //  window.localStorage.setItem('token', _res.data.data.token)
//                      //  window.localStorage.setItem('uid', _res.data.data.uid)
//                      //  强制将跳转页面设为异步
//                      router.replace('/apps')
//                      setTimeout(() => {
//                        window.location.reload()
//                      }, 0)
//                    } else {
//                      Message.error(_res.data.msg)
//                    }
//                  }).catch((_err) => {
//                    console.log(_err)
//                  })
                  router.replace('/index')
                  setTimeout(() => { location.reload() }, 1000)
                }
              })
            } else {
              Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.sign-up {
  h2 {
    text-align: center;
    font-weight: normal;
    font-size: 18px;
  }
  .form-box {
    width: 380px;
    margin: auto;
    .link {
      margin: -20px 0 0;
      span, a {
        font-size: 14px;
      }
      .right {
        float: right;
      }
    }
  }
}
</style>



