<template>
  <div class="sub-content bind-domain">
    <h2>绑定域名</h2>
    <div class="inner-box">
      <p>第一步：在你的域名解析里，添加一天 CNZME 记录，值为 <span style="color: #ed3f14">proxy.dafuv.com</span></p>
      <p>一般情况下，在域名托管商的用户界面，可以在“域名管理”功能模块中找到类似“别名”或“cname”的功能设置，进行cname记录的设置。您可以参考下列示例进行设置。</p>
      <ul>
        <li><a href="#" target="_blank">新网 cname 接入示例</a></li>
        <li><a href="#" target="_blank">万网 cname 接入示例</a></li>
        <li><a href="#" target="_blank">西部数码 cname 接入示例</a></li>
      </ul>
      <p>第二步：在域名生效后，在下面绑定你自己设置的域名（域名生效一般需要10分钟左右）</p>
      <Form
        ref="formBindDomain"
        :model="formBindDomain"
        :rules="ruleBindDomain"
        :label-width="120">
        <FormItem label="域　　名：" prop="domain">
          <Input :readonly="userInfo.private_domain !== ''" v-model="formBindDomain.domain" placeholder="请输入你要绑定的域名如：test.xxx.com（不需要填写http）"/>
        </FormItem>
        <FormItem>
          <template v-if="userInfo.private_domain">
            <Button
              type="ghost"
              @click="handleUnbind">解 绑</Button>
          </template>
          <template v-else>
            <Button
              type="primary"
              @click="handleBind('formBindDomain')">绑 定</Button>
          </template>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageBindDomain',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formBindDomain: {
        domain: this.userInfo.private_domain
      },
      ruleBindDomain: {
        domain: [
          {required: true, message: '域名不能为空', trigger: 'click'},
          {message: '域名格式不正确', pattern: /^.+\..+\..+$/, trigger: 'click'}
        ]
      }
    }
  },
  created () {
    console.log(this.userInfo.private_domain)
  },
  methods: {
    handleBind (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$ajax.post('/api/userInfo/bindPrivateDomain', this.formBindDomain).then((res) => {
            if (res.data && res.data.code === '200' && res.data.data.ret === '1') {
              this.$Message.success({
                content: '域名绑定成功',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    },
    handleUnbind () {
      this.$ajax.get('/api/userInfo/unBindPrivateDomain').then((res) => {
        if (res.data && res.data.code === '200' && res.data.data.ret === '1') {
          this.$Message.success({
            content: '域名解绑成功',
            duration: 1,
            onClose () {
              window.location.reload()
            }
          })
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.bind-domain .inner-box {
  p {
    margin-bottom: 10px;
  }
  ul {
    list-style: disc;
    margin-bottom: 20px;
    padding-left: 32px;
  }
}
</style>



