<template>
  <div class="content login">
    <div class="wrapper">
      <Card :padding="45" shadow>
        <h2 slot="title">用户登录</h2>
        <div class="form-box" @keyup.enter="handleSubmit('formLogIn')">
          <Form
            ref="formLogIn"
            :model="formLogIn"
            :rules="ruleLogIn">
            <FormItem prop="mobile">
              <Input
                type="text"
                v-model="formLogIn.mobile"
                placeholder="请输入手机号">
              <Icon
                type="android-phone-portrait"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem prop="password">
              <Input
                type="password"
                v-model="formLogIn.password"
                placeholder="请输入密码，不少于6个字符">
              <Icon
                type="android-unlock"
                size="16"
                slot="prepend"/>
              </Input>
            </FormItem>
            <FormItem class="checkbox">
              <Checkbox
                v-model="formLogIn.autoLogIn"
                :true-value="1"
                :false-value="0">记住登录状态
              </Checkbox>
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                size="large"
                shape="circle"
                icon="log-in"
                long
                @click="handleSubmit('formLogIn')">登 录
              </Button>
            </FormItem>
            <FormItem class="link"><span>还没有账号？</span> <a href="javascript:void(0);" @click="$router.push('/sign_up')">免费注册</a><a
              href="javascript:void(0);" class="right" @click="$router.push('/retrieve_pwd')">忘记密码？</a></FormItem>
          </Form>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import { getUserToken, writeUserToken } from '../utils/GetAxiosBaseValue'

export default {
  name: 'PageLogin',
  data () {
    return {
      redirect: '/index',
      formLogIn: {
        mobile: '',
        password: '',
        autoLogIn: 0
      },
      ruleLogIn: {
        mobile: [
          {required: true, message: '手机号不能为空', trigger: 'click'},
          {message: '手机号格式不正确', pattern: /^1\d{10}$/, trigger: 'click'}
        ],
        password: [
          {required: true, message: '密码不能为空', trigger: 'click'},
          {type: 'string', min: 6, message: '密码不能少于6个字符', trigger: 'click'}
        ]
      }
    }
  },
  created () {
    if (this.$route.hash) {
      this.redirect = this.$route.hash.replace('#', '/')
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$ajax.post('/user/login', this.formLogIn).then((res) => {
            if (res.data && res.data.code === '200') {
              window.sessionStorage.setItem('isLogin', true)
              writeUserToken(res.data.data.token)
              this.$ajax.defaults.headers = getUserToken()
              const redirect = this.redirect
              const router = this.$router
              this.$Message.success({
                content: '登录成功，即将跳转...',
                duration: 3,
                onClose () {
                  router.push(redirect)  //  用 replace 是阻止用户返回登录页
                  setTimeout(() => { location.reload() }, 1000)
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.login {
  h2 {
    text-align: center;
    font-weight: normal;
    font-size: 18px;
  }
  .form-box {
    width: 380px;
    margin: auto;
    .ivu-checkbox-wrapper {
      font-size: 14px;
    }
    .checkbox {
      margin: -5px 0 10px;
    }
    .link {
      margin: -20px 0 0;
      span, a {
        font-size: 14px;
      }
      .right {
        float: right;
      }
    }
  }
}
</style>



