const DAFUPAGESERVER = {
  'DEV': 'vue.dafu.im',
  'TEST': 'test.dafu.im',
  'PROD': 'dafu.im'
}

const DAFUAPISERVER = {
  'DEV': 'https://api.dafu.im',
  'TEST': 'https://dev.dafu.im',
  'PROD': 'https://api.dafu.im'
}

export const getBaseUrl = () => {
  const hostName = location.hostname
  switch (hostName) {
    case DAFUPAGESERVER.DEV:
      return DAFUAPISERVER.DEV
    case DAFUPAGESERVER.TEST:
      return DAFUAPISERVER.TEST
    case DAFUPAGESERVER.PROD:
      return DAFUAPISERVER.PROD
    default:
      return DAFUAPISERVER.DEV
  }
}

export const getUserToken = () => {
  return {
    token: String(localStorage.getItem('userToken'))
  }
}

export const writeUserToken = (token) => {
  return localStorage.setItem('userToken', token)
}



