export default (input) => {
  const filePromiseArray = []
  const { files } = input
  for (let n = 0; n < files.length; n++) {
    filePromiseArray.push(new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.onloadend = () => {
        resolve(fileReader.result)
      }
      fileReader.onerror = () => {
        reject(new Error('GetFileImageBase64: transfer file to Base64 error!'))
      }
      fileReader.readAsDataURL(files[n])
    }))
  }
  return Promise.all(filePromiseArray)
}



