<template>
  <div class="content apps">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release">发布</BreadcrumbItem>
        <BreadcrumbItem to="/apps_log">日志列表</BreadcrumbItem>
        <BreadcrumbItem>日志明细</BreadcrumbItem>
      </Breadcrumb>
      <Tabs value="apps_log" @on-click="handleChangeTab">
        <TabPane v-for="item in menus" :key="item.id" :label="item.name" :name="item.id"></TabPane>
      </Tabs>
      <Card class="min-height">
        <Table stripe :columns="columns" :data="list"></Table>
        <div class="pages">
          <Page
            v-if="total > 10"
            :total="total"
            show-total
            show-elevator
            @on-change="handlePageChange"/>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageAppsLogDetail',
  data () {
    return {
      menus: [
        {id: 'apps', name: '应用管理'},
        {id: 'apps_log', name: '下载日志'},
        {id: 'apps_list', name: '应用列表'}
      ],
      page: 1,
      total: 0,
      columns: [
        {
          title: '图标',
          key: 'icon',
          align: 'center',
          render: (h, params) => {
            return h('img', {
              attrs: {
                src: `//pic.dafu.im/icons/${params.row.app_id}.png?${new Date(params.row.create_dt).getTime()}&imageView2/1/w/120/h/120//ignore-error/1`,
                height: 32,
                width: 32
              }
            })
          }
        },
        {title: '名称', key: 'app_name', align: 'center'},
        {title: '下载IP', key: 'ip', align: 'center'},
        {title: '下载时间', key: 'create_dt', align: 'center'}
      ],
      list: []
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.handleSearch()
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    handleChangeTab (path) {
      this.$router.push(`/${path}`)
    },
    handleSearch () {
      const params = {
        app_id: this.$route.params.id,
        date: this.$route.params.date,
        page: this.page
      }
      this.$ajax.get('/api/apps/logsDetail', {params}).then((res) => {
        if (res.data && res.data.code === '200') {
          const data = res.data.data
          this.total = parseInt(data.pages.total, 10)
          if (data.logs) this.list = data.logs
        } else {
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handlePageChange (page) {
      this.page = page
      this.handleSearch()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.apps {
  .wrapper {
    overflow: hidden;
    .clear {
      margin-bottom: 20px;
    }
    button {
      margin-right:10px;
    }
    .search-box {
      float: right;
      button {
        margin: 0 0 0 10px;
      }
    }
  }
}
.pages {
  margin-top: 20px;
}
</style>



