<template>
  <div id="app" v-if="!delayed">
    <the-header :is-login="isLogin" :user-info="userInfo"/>
    <router-view/>
    <the-footer/>
    <the-float-bar/>
  </div>
</template>

<script>
import TheHeader from '@/components/TheHeader'
import TheFooter from '@/components/TheFooter'
import TheFloatBar from '@/components/TheFloatBar'

export default {
  name: 'app',
  components: {
    TheHeader,
    TheFooter,
    TheFloatBar
  },
  data () {
    return {
      isLogin: false,
      userInfo: {},
      delayed: true //  延迟模块加载，防止闪屏
    }
  },
  watch: {
    '$route' (to, from) {
      //  监控路由变化，如果是从登录页面跳转过来则请求用户数据
      if (from.path === '/login') {
        this.queryUserInfo()
      }
    }
  },
  created () {
    this.queryUserInfo().then(() => {
      this.delayed = false
    })
  },
  methods: {
    //  验证登录态并获取用户信息
    queryUserInfo () {
      return this.$ajax.get('/api/userInfo/get').then((res) => {
        if (res.data && res.data.code === '200') {
          this.isLogin = true
          this.userInfo = res.data.data
          this.$store.userInfo = res.data.data
          window.localStorage.setItem('isLogin', true)
        } else {
          this.isLogin = false
          window.localStorage.removeItem('isLogin')
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="scss">
html {
  height: 100%;
  body {
    height: 100%;
    margin: 0;
  }
}
#app {
  position: relative;
  min-width: 1200px;
  min-height: 100%;
  padding-bottom: 300px;
  font: 14px/1.5 'Microsoft Yahei', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  background: #f5f8fb;
  box-sizing: border-box;
  .ivu-input {
    font-size: 14px;
  }
  //  头部样式
  .header .wrapper {
    .ivu-dropdown-rel {
      line-height: 1;
    }
    .ivu-select-dropdown {
      z-index: 999;
    }
    .ivu-dropdown-item {
      font-size: 14px!important;
      .ivu-icon {
        margin-right: 5px;
        font-size: 14px
      }
    }
  }
  //  首页样式
  .ivu-carousel-dots-inside {
    bottom: 5%;
  }
  .ivu-carousel-dots {
    li {
      margin: 0 8px;
      button.radius {
        height: 16px;
        width: 16px;
        border: 1px solid #fff;
      }
    }
    li.ivu-carousel-active > button.radius {
      width: 16px;
      background: #2d8cf0;
    }
  }
  .ivu-modal-confirm-body-icon-confirm {
    top: 10px;
  }
  //  内页样式
  .sub-content {
    padding-bottom: 32px;
    min-height: 500px;
    h2 {
      height: 50px;
      margin: 0 32px 20px;
      font-size: 18px;
      line-height: 50px;
      text-align: center;
      border-bottom: 1px solid #f5f8fb;
    }
    .inner-box {
      padding: 0 32px;
    }
    .ivu-form .ivu-form-item-label,
    .ivu-form-item-content {
      font-size: 14px;
    }
  }
  .sub-content::before {
    content: '';
    position: absolute;
    display: block;
    width: 1px;
    background: #dddee1;
    top: 0;
    bottom: 0;
    left: -1px;
    z-index: 1;
  }
  .sidebar {
    padding-bottom: 32px;
  }
}
.clear:before,
.clear:after{
  content: ' ';
  display: table;
}
.clear:after {
  clear: both;
}
.wrapper {
  width: 90%;
  min-width: 1160px;
  margin: 0 auto;
}
.content {
  padding: 32px 0;
}
//  签名页面样式
.sign {
  td {
    padding: 5px 0 7px;
    font-size: 16px;
    text-align: center;
  }
  th {
    padding: 5px 0 7px;
    font-size: 20px;
    font-weight: normal;
    text-align: center;
  }
}
.ivu-table-cell button {
  margin: 0 5px;
}
.min-height {
  .ivu-card-body {
    min-height: 500px;
  }
}
.ivu-message {
  font-size: 16px!important;
  .ivu-icon {
    font-size: 18px!important;
  }
}
.bread-crumb {
  margin-bottom: 20px;
}
.app-icon .ivu-upload-list, .app-list-create .ivu-upload-list {
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  .ivu-upload-list-file {
    span, i {
      display: none;
    }
  }
}
.app-list-create .ivu-upload-list {
  width: 576px;
}
</style>



