<template>
  <div class="content setting">
    <div class="wrapper">
      <Row>
        <Col span="4">
        <Menu
          class="sidebar"
          theme="light"
          width="auto"
          :active-name="activeName"
          @on-select="handleSelect">
          <MenuGroup title="基本设置">
            <MenuItem
              v-for="item in menus"
              :key="item.id"
              :name="item.id">
              <span class="icon"><Icon :type="item.icon"/></span>
              {{item.name}}
            </MenuItem>
          </MenuGroup>
        </Menu>
        </Col>
        <Col span="20">
        <template>
          <router-view :user-info="userInfo"></router-view>
        </template>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageSetting',
  data () {
    return {
      activeName: '',
      menus: [
        {id: 'account', name: '账号信息', icon: 'android-person'},
        {id: 'modify_pwd', name: '修改密码', icon: 'key'} //  ,
//        {id: 'bind_qiniu', name: '绑定七牛云', icon: 'android-cloud'},
//        {id: 'bind_domain', name: '绑定域名', icon: 'android-navigate'}
        //  {id: 'pay_list', name: '购买记录', icon: 'cash'},
        //  {id: 'my_pack', name: '我的封装', icon: 'cube'},
        //  {id: 'my_sign', name: '我的签名', icon: 'ios-compose'},
        //  {id: 'progress', name: '上架进度', icon: 'arrow-graph-up-right'},
        //  {id: 'invoice', name: '发票管理', icon: 'document'},
        //  {id: 'site_msg', name: '消息通知', icon: 'email'}
      ],
      userInfo: {}
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.userInfo = this.$store.userInfo
      this.activeName = this.$route.path.split('/')[2]
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  methods: {
    handleSelect (id) {
      this.$router.push(`/setting/${id}`)
      this.activeName = id
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.setting {
  .wrapper {
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .icon {
    display: inline-block;
    width: 16px;
    text-align: center;
  }
}
</style>



