import Vue from 'vue'
import Router from 'vue-router'
import PageNotFound from '@/components/PageNotFound'
import PageLogin from '@/components/PageLogin'
import PageSignUp from '@/components/PageSignUp'
import PageModifyPwd from '@/components/PageModifyPwd'
import PageRetrievePwd from '@/components/PageRetrievePwd'
import PageHome from '@/components/PageHome'
import PagePack from '@/components/PagePack'
import PageSale from '@/components/PageSale'
import PageSign from '@/components/PageSign'
import PageRelease from '@/components/PageRelease'
import PageKit from '@/components/PageKit'
import PageUdid from '@/components/PageUdid'
import PageExtract from '@/components/PageExtract'
import PagePrice from '@/components/PagePrice'
import PagePayWeixin from '@/components/PagePayWeixin'
import PageSetting from '@/components/PageSetting'
import PageApps from '@/components/PageApps'
import PageAppsLog from '@/components/PageAppsLog'
import PageAppsLogDetail from '@/components/PageAppsLogDetail'
import PageAppsList from '@/components/PageAppsList'
import PageAppsListCreate from '@/components/PageAppsListCreate'
import PageAppsSetting from '@/components/PageAppsSetting'
import PageAppsPreview from '@/components/PageAppsPreview'
import PageService from '@/components/PageService'
import PageAccount from '@/components/PageAccount'
import PageBindQiniu from '@/components/PageBindQiniu'
import PageBindDomain from '@/components/PageBindDomain'
import PagePayList from '@/components/PagePayList'
import PageMyPack from '@/components/PageMyPack'
import PageMySign from '@/components/PageMySign'
import PageProgress from '@/components/PageProgress'
import PageInvoice from '@/components/PageInvoice'
import PageInvoiceDetail from '@/components/PageInvoiceDetail'
import PageCoupon from '@/components/PageCoupon'
import PageAdData from '@/components/PageAdData'
import PageSiteMsg from '@/components/PageSiteMsg'
import PageArticle from '@/components/PageArticle'
import PagePackPurchase from '@/components/PagePackPurchase'
import PagePackSetting from '@/components/PagePackSetting'
import PagePackResult from '@/components/PagePackResult'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      name: 'PageNotFound',
      component: PageNotFound
    },
    {
      path: '/',
      name: 'PageHome',
      component: PageHome
      // redirect: '/home'
    },
    {
      path: '/login',
      name: 'PageLogin',
      component: PageLogin
    },
    {
      path: '/sign_up',
      name: 'PageSignUp',
      component: PageSignUp
    },
    {
      path: '/retrieve_pwd',
      name: 'PageRetrievePwd',
      component: PageRetrievePwd
    },
    {
      path: '/home',
      name: 'PageHome',
      component: PageHome
    },
    {
      path: '/pack',
      name: 'PagePack',
      component: PagePack
    },
    {
      path: '/pack_purchase',
      name: 'PagePackPurchase',
      component: PagePackPurchase
    },
    {
      path: '/pack_setting',
      name: 'PagePackSetting',
      component: PagePackSetting
    },
    {
      path: '/pack_result',
      name: 'PagePackResult',
      component: PagePackResult
    },
    {
      path: '/sale',
      name: 'PageSale',
      component: PageSale
    },
    {
      path: '/sign',
      name: 'PageSign',
      component: PageSign
    },
    {
      path: '/release',
      name: 'PageRelease',
      component: PageRelease
    },
    {
      path: '/kit',
      name: 'PageKit',
      component: PageKit
    },
    {
      path: '/udid',
      name: 'PageUdid',
      component: PageUdid
    },
    {
      path: '/extract',
      name: 'PageExtract',
      component: PageExtract
    },
    {
      path: '/price',
      name: 'PagePrice',
      component: PagePrice
    },
    {
      path: '/pay_weixin',
      name: 'PagePayWeixin',
      component: PagePayWeixin
    },
    {
      path: '/setting',
      name: 'PageSetting',
      component: PageSetting,
      redirect: '/setting/account',
      children: [
        {
          path: 'account',
          name: 'PageAccount',
          component: PageAccount
        },
        {
          path: 'modify_pwd',
          name: 'PageModifyPwd',
          component: PageModifyPwd
        },
        {
          path: 'bind_qiniu',
          name: 'PageBindQiniu',
          component: PageBindQiniu
        },
        {
          path: 'bind_domain',
          name: 'PageBindDomain',
          component: PageBindDomain
        },
        {
          path: 'pay_list',
          name: 'PagePayList',
          component: PagePayList
        },
        {
          path: 'my_pack',
          name: 'PageMyPack',
          component: PageMyPack
        },
        {
          path: 'my_sign',
          name: 'PageMySign',
          component: PageMySign
        },
        {
          path: 'progress',
          name: 'PageProgress',
          component: PageProgress
        },
        {
          path: 'invoice',
          name: 'PageInvoice',
          component: PageInvoice
        },
        {
          path: 'invoice_detail/:id',
          name: 'PageInvoiceDetail',
          component: PageInvoiceDetail
        },
        {
          path: 'site_msg',
          name: 'PageSiteMsg',
          component: PageSiteMsg
        }
      ]
    },
    {
      path: '/apps',
      name: 'PageApps',
      component: PageApps
    },
    {
      path: '/apps_log',
      name: 'PageAppsLog',
      component: PageAppsLog
    },
    {
      path: '/apps_log_detail/:id/:date',
      name: 'PageAppsLogDetail',
      component: PageAppsLogDetail
    },
    {
      path: '/apps_list',
      name: 'PageAppsList',
      component: PageAppsList
    },
    {
      path: '/apps_list_create',
      name: 'PageAppsListCreate',
      component: PageAppsListCreate
    },
    {
      path: '/apps_setting/:id',
      name: 'PageAppsSetting',
      component: PageAppsSetting
    },
    {
      path: '/apps_preview/:id',
      name: 'PageAppsPreview',
      component: PageAppsPreview
    },
    {
      path: '/service',
      name: 'PageService',
      component: PageService,
      redirect: '/service/coupon',
      children: [
        {
          path: 'coupon',
          name: 'PageCoupon',
          component: PageCoupon
        },
        {
          path: 'ad_data',
          name: 'PageAdData',
          component: PageAdData
        }
      ]
    },
    {
      path: '/article/:key',
      name: 'PageArticle',
      component: PageArticle
    }
  ]
})
export default router



